import React, { useEffect, useLayoutEffect, useState } from "react";

import {
  Table,
  Divider,
  Button,
  Input,
  Select,
  Row,
  Col,
  Card,
  Typography,
  Collapse,
  Switch,
  Form,
  Modal,
  Spin,
} from "antd";
import { connect } from "react-redux";
import {
  RiInformationLine,
  RiArrowRightSLine,
  RiArrowLeftCircleFill,
  RiAddCircleFill,
  RiAlertLine,
} from "react-icons/ri";

import { Link, useHistory, useParams } from "react-router-dom";
import {
  getPermissionsList,
  setProfileParams,
  getProfileList,
  InputChangeValue,
  checkAppPermission,
  checkPermission,
  submitProfile,
  getProfilebyID,
  clearProfileData,
} from "../../../../store/settings/ManageProfile/profileSlice";

function ProfileAdd(props) {
  let history = useHistory();
  const { Paragraph, Text } = Typography;
  const { confirm } = Modal;
  const { Panel } = Collapse;
  const [form] = Form.useForm();
  const { Option } = Select;
  let { id } = useParams();
  const dividerClass = "hp-border-color-black-40 hp-border-color-dark-80";
  useEffect(() => {
    props.getProfileList({ page: 1, search: "", page_size: 100 });
    props.getPermissionsList();
    if (id != 0) {
      props.getProfilebyID(id);
    } else {
      form.resetFields();
      props.clearProfileData();
    }
  }, []);
  if (id != 0) {
    useEffect(() => {
      props.getProfilebyID(id);
    }, [props.appPermissionsList.length != 0]);
  }
  const locationTypeList = [
    { id: 1, name: "Counter" },
    { id: 2, name: "Cluster" },
    { id: 3, name: "Division" },
    { id: 4, name: "Zone" },
  ];
  function ChangeHandeler(e) {
    props.InputChangeValue({
      key: e.target.name,
      value: e.target.value,
    });
  }
  // Adds New Profile
  const handleAdd = () => {
    var permission_ids = [];
    permission_ids = props.checkedPermissions.map((permission) => {
      return permission.id;
    });
    const profileObj = {
      ...props.profileData,
      id: id,
      permission_ids: permission_ids,
    };
    if (profileObj.permission_ids.length !== 0) {
      confirm({
        title: (
          <h5 className="hp-mb-0 hp-font-weight-500">
            Are you sure save Profile?
          </h5>
        ),
        icon: (
          <span className="remix-icon">
            <RiInformationLine />
          </span>
        ),

        okText: "Yes",
        okType: "danger",
        cancelText: "No",
        onOk() {
          props.submitProfile({ profileObj, history });
        },
        onCancel() {},
      });
      // props.versionfeaturesAdd(obj);
    } else {
      Modal.warning({
        icon: (
          <span className="remix-icon">
            <RiAlertLine />
          </span>
        ),
        title: <h5 className="hp-mb-0 hp-font-weight-500">Permission Error</h5>,
        content: (
          <div>
            <p className="hp-p1-body hp-text-color-black-80">
              please select at least one permission.
            </p>
          </div>
        ),
      });
    }
  };

  function callback(key) {
    console.log(key);
  }
  const genExtra = () => (
    <RiArrowRightSLine
      size={24}
      className="hp-collapse-arrow hp-text-color-black-60"
    />
  );
  function renderPermissons() {
    return props.appPermissionsList.map((item, index) => {
      return (
        <Panel
          header={
            <div onClick={(e) => e.stopPropagation()}>
              {item.name + " Permission"}
              <Switch
                id={"appPermissionsChecked" + index}
                checkedChildren="On"
                unCheckedChildren="Off"
                // defaultChecked finalapproval
                className="hp-mb-6 hp-ml-6"
                checked={item.appPermissionsChecked}
                onChange={(value) => {
                  props.checkAppPermission({
                    appPermission_id: item.id,
                    appPermission_checked: value,
                  });
                }}
              />
            </div>
          }
          key={index + 1}
          showArrow={false}
          extra={genExtra()}
        >
          {renderPermissonsCard(item, index)}
        </Panel>
      );
    });
  }
  function renderPermissonsCard(item, index) {
    return (
      <>
        {item.contenttypedetails.map((contenttype, index) => (
          <Card className="hp-mb-6 hp-p-0" key={index}>
            <Row gutter={(12, 12)}>
              <Col md={8}>
                <Text className="h5">{contenttype.name}</Text>
              </Col>

              <Col md={16}>
                <Row gutter={(12, 12)}>
                  {contenttype.permissions.map((permission, index) => {
                    return (
                      <Col key={index}>
                        <Switch
                          id={"permissionChecked" + index}
                          checkedChildren="On"
                          unCheckedChildren="Off"
                          className="hp-mb-6 hp-mr-6"
                          checked={permission?.permissionChecked}
                          onChange={(value) => {
                            props.checkPermission({
                              appPermission_id: item.id,
                              contentType_id: contenttype.id,
                              permission_id: permission.id,
                            });
                          }}
                        />
                        <Text className="h5">
                          {permission?.permissiondetails?.name}
                        </Text>
                      </Col>
                    );
                  })}
                </Row>
              </Col>
            </Row>
          </Card>
        ))}
      </>
    );
  }

  console.log("props.profileList", props.profileList);

  useEffect(() => {
    form.setFieldsValue({
      name: props.profileData?.name,
      locationtype: props.profileData.locationtype,
      reportingto_id: props.profileData.reportingto_id,
    });
  }, [props.profileData]);
  return (
    <div className="openFilter hp-purchase-details">
      <div className="hp-profile-activity">
        <Row>
          <Col span={18}>
            <Link
              to="#"
              onClick={() => history.goBack()}
              className="action-icon text-primary  hp-mr-12 hp-d-inline-block"
            >
              <RiArrowLeftCircleFill className="remix-icon" size={24} />
            </Link>

            <Row className="hp-d-inline-block">
              <Col span={24}>
                <h2 className="hp-m-0">
                  {" "}
                  {id == 0 ? "Add" : "Update"} Profiles
                </h2>
              </Col>
            </Row>
          </Col>
          <Col span={6}>
            <Button
              className="hp-float-right"
              type="primary"
              onClick={() => form.submit()}
              block
            >
              Save
            </Button>
          </Col>
        </Row>

        <Divider className={dividerClass} />
        <Form
          layout="vertical"
          form={form}
          autoComplete="off"
          onFinish={() => {
            handleAdd();
          }}
        >
          <Row gutter={[12, 12]}>
            <Col span={24} xs={24} xl={8} md={8}>
              <Form.Item
                label="Profile Name :"
                name="name"
                rules={[
                  {
                    required: true,
                    message: "Please Input Profile Name",
                  },
                ]}
                hasFeedback
              >
                <Input
                  id="name"
                  name="name"
                  style={{ width: "100%" }}
                  placeholder="Profile Name"
                  value={
                    props.profileData.name == null
                      ? ""
                      : props.profileData?.name
                  }
                  maxLength={20}
                  onChange={ChangeHandeler}
                  rules={[
                    {
                      required: true,
                      message: "Please input your Profile Name!",
                    },
                  ]}
                />
              </Form.Item>
            </Col>
            <Col span={24} xs={24} xl={8} md={8}>
              <Form.Item
                label="Location Type"
                name="locationtype"
                rules={[
                  {
                    required: true,
                    message: "Please Select Location Type",
                  },
                ]}
                hasFeedback
              >
                <Select
                  showSearch
                  placeholder="Select Location Type"
                  optionFilterProp="locationtype"
                  // disabled
                  filterOption={(input, option) =>
                    option.children.toLowerCase().includes(input.toLowerCase())
                  }
                  onChange={(value) => {
                    props.InputChangeValue({
                      key: "locationtype",
                      value: value,
                    });
                  }}
                  value={
                    props.profileData.locationtype == null
                      ? ""
                      : props.profileData?.locationtype
                  }
                >
                  {locationTypeList.map((locationType, index) => {
                    return (
                      <Option key={index} value={locationType.id}>
                        {locationType.name}
                      </Option>
                    );
                  })}
                </Select>
              </Form.Item>
            </Col>
            <Col span={24} xs={24} xl={8} md={8}>
              <Form.Item
                label="Reporting To"
                name="reportingto_id"
                rules={[
                  {
                    required: true,
                    message: "Please Select Reporting To",
                  },
                ]}
                hasFeedback
              >
                <Select
                  showSearch
                  placeholder="Select Reporting To"
                  optionFilterProp="reportingto_id"
                  onSearch={(value) => {
                    props.setProfileParams({
                      ...props.profileParams,
                      page: 1,
                      search: value,
                    });
                    props.getProfileList({
                      ...props.profileParams,
                      page: 1,
                      search: value,
                    });
                  }}
                  defaultActiveFirstOption={false}
                  showArrow={true}
                  filterOption={false}
                  onChange={(value) => {
                    props.InputChangeValue({
                      key: "reportingto_id",
                      value: value,
                    });
                  }}
                  onPopupScroll={() => {
                    if (
                      props.profileParams.page < props.profileParams.no_of_pages
                    ) {
                      props.setProfileParams({
                        ...props.profileParams,
                        page: props.profileParams.page + 1,
                      });
                      props.getProfileList({
                        ...props.profileParams,
                        page: props.profileParams.page + 1,
                      });
                    }
                  }}
                  value={
                    props.profileData.reportingto_id == null
                      ? ""
                      : props.profileData?.reportingto_id
                  }
                >
                  {props.profileList.map((profile, index) => {
                    return (
                      <Option key={index} value={profile.id}>
                        {profile.name}
                      </Option>
                    );
                  })}
                </Select>
              </Form.Item>
            </Col>
          </Row>
        </Form>

        <Spin tip="Loading..." spinning={props.permissionsLoading} delay={500}>
          <Collapse
            defaultActiveKey={[1]}
            onChange={callback}
            accordion
            expandIconPosition="end"
            expandIcon={() => <RiArrowRightSLine />}
          >
            {renderPermissons()}
          </Collapse>
        </Spin>
      </div>
    </div>
  );
}
function mapStateToProps({ manageProfile }) {
  return {
    appPermissionsList: manageProfile.appPermissionsList,
    profileData: manageProfile.profileData,
    profileList: manageProfile.profileList,
    profileParams: manageProfile.profileParams,
    checkedPermissions: manageProfile.checkedPermissions,
    // loading: manageProfile.loading,
    permissionsLoading: manageProfile.permissionsLoading,
  };
}

export default connect(mapStateToProps, {
  getPermissionsList,
  setProfileParams,
  getProfileList,
  InputChangeValue,
  checkAppPermission,
  checkPermission,
  submitProfile,
  getProfilebyID,
  clearProfileData,
})(ProfileAdd);
