import React, { useEffect, useState } from "react";

import {
  Table,
  Divider,
  Button,
  icon,
  Row,
  Col,
  Input,
  Space,
  Avatar,
  Drawer,
  Layout,
  Pagination,
  Popconfirm,
  Switch,
  Tabs,
  Card,
} from "antd";
import { Search } from "react-iconly";
import {
  EyeOutlined,
  FilterOutlined,
  UploadOutlined,
  UserOutlined,
} from "@ant-design/icons";
import { connect } from "react-redux";
import Swal from "sweetalert2";
import { Link } from "react-router-dom";
import { RiErrorWarningLine } from "react-icons/ri";
import moment, { min } from "moment";
import {
  getUsers,
  isModelVisible,
  employeeUserDelete,
  userActive,
  userInActive,
  isPasswordModel,
  getUserID,
  setUsersParams,
  isDrawerVisible
} from "../../../../store/settings/ManageUsers/manageUserSlice";
import {
  getMinicountry,
  getMiniStates,
  getMiniDepartment,
  getMiniFirm,
} from "../../../../store/mini/miniSlice";
import FilterMenu from "../filter/FilterMenu";
import ChangePassword from "./changePassword";
import { PlusCircleOutlined, MinusCircleOutlined } from "@ant-design/icons";
import { isMobile } from "react-device-detect";
import { getReportList } from "../../../../store/reports/reportsSlice";
import Filter from "./Filters/Filter";

function ManageUsers(props) {
  const { Search } = Input;
  const columnClass =
    "hp-p1-body hp-text-color-black-100 hp-text-color-dark-0 hp-font-weight-600 ";
  const dividerClass = "hp-border-color-black-40 hp-border-color-dark-80";

  useEffect(() => {
    props.getUsers({ page: 1, search: "", page_size: 10 });
    props.getMinicountry();
    props.getMiniStates();
    props.getMiniDepartment();
    props.getMiniFirm();
  }, []);

  // const [openFilter, setOpenFilter] = useState(false);
  const [visible, setVisible] = useState(false);
  const { TabPane } = Tabs;
  const showDrawer = () => {
    setVisible(true);
  };

  const onClose = () => {
    setVisible(false);
  };

  const openFilter = () => {
    // debugger
    setVisible(true);
  };

  const columns = [
    {
      title: "S.No",
      width: 10,
      render: (row) => <span className={columnClass}>{row.index}</span>,
    },
    {
      title: " Code",
      width: 150,
      key: "code",
      render: (row) => (
        <span className={columnClass}>{row.code}</span>
      ),
    },
    {
      title: "UserName",
      width: 150,
      render: (row) => <span className={columnClass}>{row.user?.username}</span>,
    },
    {
      title: "Name",
      width: 200,
      key: "name",
      render: (row) => (
        <span className={columnClass}>
          {row.user.first_name + " " + row.user.last_name}
        </span>
      ),
    },
    {
      title: "Email",
      width: 200,
      key: "email",
      render: (row) => <span className={columnClass}>{row.user?.email}</span>,
    },
    {
      title: "Phone",
      width: 150,
      key: "phone",
      render: (row) => <span className={columnClass}>{row.user?.phone}</span>,
    },
    {
      title: "Role",
      width: 150,
      key: "role",
      render: (row) => (
        <span className={columnClass}>{row.user.groups[0]?.name}</span>
      ),
    },
    {
      title: "Password",
      width: 150,
      key: "password",
      render: (row) => (
        <Space size="middle">
          <Button
            size={"small"}
            type="dashed"
            disabled={!row.user.is_active && props.userAccessList?.indexOf("Masters.change_employee") !== -1}
            onClick={() => {
              props.isPasswordModel(true);
              props.getUserID(row.user?.id);
            }}
          >
            Change Password
          </Button>
        </Space>
      ),
    },
    {
      title: "Status",
      width: 150,
      key: "status",
      render: (row) => (
        <Space size="middle">
          <Switch
            id="mcapplicable"
            size="default"
            checkedChildren="Active"
            unCheckedChildren="InActive"
            checked={row.user.is_active}
            onChange={(value) => {
              if (value) {
                props.userActive(row.user?.id);
              } else {
                props.userInActive(row.user?.id);
              }
            }}
          />
        </Space>
      ),
    },

    {
      title: "Actions",
      key: "action",
      sorter: true,
      width: 100,
      render: (row) => (
        <Space size="middle">
          {props.userAccessList?.indexOf("Masters.change_employee") !== -1 && (
            <Link to={`/pages/settings/manage-users/` + row.id}>
              <i
                className="ri-edit-box-line hp-mr-2 "
                style={{ cursor: "pointer", color: "#fc6f03" }}
              ></i>
            </Link>
          )}

          <Link to={`/pages/settings/user-details/` + row.id}>
            <i
              className="ri-eye-line hp-mr-2 "
              style={{ cursor: "pointer", color: "#fc6f03" }}
            ></i>
          </Link>

          {/* {props.userAccessList?.indexOf("Masters.delete_employee") !== -1 && (
            <Popconfirm
              placement="topRight"
              title="Are you sure to delete this Record?"
              onConfirm={() => {
                props.employeeUserDelete(row.id);
              }}
              disabled={row.groupdetails?.static}
              okText="Yes"
              cancelText="No"
              className="hp-ml-8"
              icon={
                <RiErrorWarningLine className="remix-icon hp-text-color-primary-1" />
              }
            >
              <i
                className="ri-delete-bin-2-line "
                style={{ cursor: "pointer", color: "#ff0000" }}
              ></i>
            </Popconfirm>
          )} */}
        </Space>
      ),
    },
  ];

  const data = props.Userslist.map((row, key) => ({
    ...row,
    // key: key + 1,
    // index: key + 1,
    index:(props.usersParams.page - 1) * props.usersParams.page_size + (key + 1),
  }));

  function onSearch(val) {
    props.getUsers({
      ...props.usersParams,
      page: 1,
      search: val,
    });
    props.setUsersParams({
      ...props.usersParams,
      page: 1,
      search: val,
    });
  }
  function onPageChange(page, pageSize) {
    props.getUsers({
      ...props.usersParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
    props.setUsersParams({
      ...props.usersParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  function showTotal(total) {
    return "Total " + total + " items";
  }

 
  function exportData() {
    // var { file_format, ...params } = props.reportParamsData;
    var objData = {
      file_format: 0,
      model_name: "Employee",
    };

    props.getReportList({
      objData,
      ParamsData: {
        ...props.usersParams,
        start_date:
          props.usersParams.start_date != null &&
          props.usersParams.start_date != ""
            ? moment(props.usersParams.start_date).format("YYYY-MM-DD")
            : "",
        end_date:
          props.usersParams.end_date != null &&
          props.usersParams.end_date != ""
            ? moment(props.usersParams.end_date)
                .add(1, "days")
                .format("YYYY-MM-DD")
            : "",
      },
    });
  }
 

  return (
    <div className="openFilter">
      <div className="hp-profile-activity">
        <Row className="hp-mb-16">
          <Col md={6} span={24} sm={12} xs={12} className="hp-pr-sm-0 hp-pr-12">
            <h4 className="hp-mt-8">All Users ({props.listCount})</h4>
          </Col>
        </Row>

        <Row gutter={[12, 12]}>
          <Col
            span={24}
            xs={24}
            sm={24}
            md={11}
            lg={11}
            className="hp-mr-10 hp-mb-xs-10"
          >
            <Search
              placeholder="Search for date, doc, number, customer, counter, vehicle"
              allowClear
              onChange={(e) => {
                if (e.target.value == "") {
                  props.getUsers({
                    ...props.usersParams,
                    page: 1,
                    search: "",
                  });
                  props.setUsersParams({
                    ...props.usersParams,
                    page: 1,
                    search: "",
                  });
                }
              }}
              onSearch={(value) => {
                console.log("searcg", value, typeof value);
                onSearch(value);
              }}
            />

            {/* <Search
              className="hp-mb-12"
              placeholder="Search..."
              allowClear
              onChange={(e) => {
                if (e.target.value == "") {
                  props.getUsers({
                    ...props.usersParams,
                    page: 1,
                    search: "",
                  });
                  props.setUsersParams({
                    ...props.usersParams,
                    page: 1,
                    search: "",
                  });
                }
              }}
              onSearch={(value) => {
                onSearch(value);
              }}
            /> */}
          </Col>

          <Col span={24} xs={6} sm={6} md={4} lg={4}>
            {/* <Drawer
              placement="right"
              width={300}
              closable={false}
              onClose={onClose}
              visible={visible}
              className="hp-drawer-mobile hp-pl-0 hp-pr-0"
            >
              <FilterMenu visible={visible} setVisible={setVisible} />
            </Drawer>

            <Button
              className="hp-w-100 hp-p-10"
              onClick={showDrawer}
              icon={<FilterOutlined className="hp-mr-10" />}
              block
            >
              {!isMobile && "Filter"}
            </Button> */}
            <Button
              onClick={() => {
                props.isDrawerVisible(true);
              }}
              // className="hp-w-100 hp-p-12"
              className={
                props.isFilterActive ? "hp-bg-primary-1 hp-w-100 hp-p-12 hp-text-color-white hp-font-weight-600" : "hp-w-100"
              }
              icon={<FilterOutlined className="hp-mr-10" />}
            >
              {!isMobile && "Filter"}
            </Button>
          </Col>

          

          <Col span={24} xs={6} sm={6} md={4} lg={4}>
          {props.userAccessList?.indexOf("Masters.export_employee") !== -1 && (
            <Popconfirm
              placement="topRight"
              title="Are you sure to export this Record?"
              onConfirm={() => exportData()}
              okText="Yes"
              cancelText="No"
              icon={
                <RiErrorWarningLine className="remix-icon hp-text-color-primary-1" />
              }
            >
              <Button
                className="hp-w-100 hp-p-12"
                icon={<UploadOutlined className="hp-mr-10" />}
              >
                {!isMobile && "Export"}
              </Button>
            </Popconfirm>
          )}
          </Col>

          <Col span={24} xs={12} sm={12} md={4} lg={4}>
            {props.userAccessList?.indexOf("Masters.add_employee") !== -1 && (
              <Link to="/pages/settings/manage-users/0">
                <Button block type="primary">
                  ADD USER
                </Button>
              </Link>
            )}
          </Col>
        </Row>

        <Divider className={dividerClass} />

        <Table
          className=" hp-border-1 hp-border-color-dark-80 hp-border-bottom-none hp-border-radius"
          columns={columns}
          dataSource={data}
          loading={props.loading}
          scroll={{ x: 700 }}
          pagination={false}
          size="middle"
        />

        <Pagination
          size="small"
          total={props.listCount}
          current={props.usersParams?.page}
          pageSize={props.usersParams?.page_size}
          showTotal={showTotal}
          showSizeChanger
          // onShowSizeChange={onShowSizeChange}
          onChange={onPageChange}
          className="hp-mb-12 hp-mt-24 hp-float-right"
        />
        <ChangePassword />
      </div>
      <Filter />
    </div>
  );
}
function mapStateToProps({ manageUsers, system }) {
  return {
    Userslist: manageUsers.Userslist,
    noofpages: manageUsers.noofpages,
    listCount: manageUsers.listCount,
    loading: manageUsers.loading,
    usersParams: manageUsers.usersParams,
    userAccessList: system.userAccessList,
  };
}

export default connect(mapStateToProps, {
  getUsers,
  employeeUserDelete,
  isModelVisible,
  getMinicountry,
  getMiniStates,
  getMiniDepartment,
  getMiniFirm,
  userActive,
  userInActive,
  isPasswordModel,
  getUserID,
  setUsersParams,
  getReportList,
  isDrawerVisible
})(ManageUsers);
