import React, { useEffect, useState } from "react";
import {
  Row,
  Col,
  Divider,
  Table,
  Pagination,
  Tabs,
  Card,
  Spin,
  Typography,
} from "antd";
import { connect } from "react-redux";
import {
  getAdminUserLogin,
  getAdminUserDeviceList,
  setUsersDeviceParams,
  userDeviceActiveTab,
  getAdminUserActivityList,
  clearAdminUserLogin,
  clearAdminUserDeviceList,
  clearAdminUserActivityList,
  getEmployeeUserbyID,
} from "../../../../store/settings/ManageUsers/manageUserSlice";
import { useParams, useHistory, Link } from "react-router-dom";
import moment, { min } from "moment";
import { RiArrowLeftCircleFill } from "react-icons/ri";
import PageTitle from "../../../../layout/components/content/page-title";

function UserDetails(props) {
  const columnClass =
    "hp-p1-body hp-text-color-black-100 hp-text-color-dark-0 hp-font-weight-600 ";
  const { TabPane } = Tabs;
  let { id } = useParams();
  let history = useHistory();
  const listTitle = "hp-p-body hp-font-weight-600 hp-mb-6";
  const listResult =
    "hp-mt-sm-4 hp-p1-body hp-text-color-black-100 hp-text-color-dark-0";
  const dividerClass = "hp-border-color-black-40 hp-border-color-dark-80";
  const { Text, Link, Title } = Typography;

  useEffect(() => {
    props.getEmployeeUserbyID(id);
    props.setUsersDeviceParams({
      ...props.UserDeviceParams,
      page: 1,
      search: "",
      page_size: 10,
      id: id,
    });
  }, []);

  function callback(key) {
    props.userDeviceActiveTab(key, id);
    if (key == 1) {
      props.getAdminUserLogin({
        page: 1,
        search: "",
        page_size: 10,
        id: props.employeeUserdata.user.id,
      });
    } else if (key == 2) {
      props.getAdminUserDeviceList({
        page: 1,
        search: "",
        page_size: 10,
        id: props.employeeUserdata.user.id,
      });
    } else {
      props.getAdminUserActivityList({
        page: 1,
        search: "",
        page_size: 10,
        id: props.employeeUserdata.user.id,
      });
    }
    props.setUsersDeviceParams({
      ...props.UserDeviceParams,
      page: 1,
      search: "",
      page_size: 10,
    });
  }

  function showTotal(total) {
    return "Total " + total + " items";
  }

  function onPageChange(page, pageSize) {
    props.clearAdminUserLogin();
    props.clearAdminUserDeviceList();
    props.clearAdminUserActivityList();
    if (props.activeTab == 1) {
      props.getAdminUserLogin({
        ...props.UserDeviceParams,
        page_size: pageSize,
        page: page == 0 ? 1 : page,
      });
    } else if (props.activeTab == 2) {
      props.getAdminUserDeviceList({
        ...props.UserDeviceParams,
        page_size: pageSize,
        page: page == 0 ? 1 : page,
      });
    } else {
      props.getAdminUserActivityList({
        ...props.UserDeviceParams,
        page_size: pageSize,
        page: page == 0 ? 1 : page,
      });
    }
    props.setUsersDeviceParams({
      ...props.UserDeviceParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  const loginColumns = [
    {
      title: "S.No",
      dataIndex: "index",
      width: "1%",
    },
    {
      title: "Device Name",
      width: "3%",
      render: (row) => <span className={columnClass}>{row.device?.name}</span>,
    },
    {
      title: "Device Type",
      width: "3%",
      render: (row) => (
        <span className={columnClass}>{row.device?.type_name}</span>
      ),
    },
    {
      title: "Login",
      width: "5%",
      render: (row) => (
        <span className={columnClass}>
          {moment(row.login).format("DD-MM-YYYY, h:mm:ss a")}
        </span>
      ),
    },
    {
      title: "Logout",
      width: "10%",
      render: (row) => (
        <span className={columnClass}>
          {moment() < moment(row.logout) ? (
            <Text className="hp-text-color-success-1">Active</Text>
          ) : (
            moment(row.logout).format("DD-MM-YYYY, h:mm:ss a")
          )}
        </span>
      ),
    },
  ];

  const loginData = props.adminUserLoginList.map((row, key) => ({
    ...row,
    // key: key + 1,
    // index: key + 1,
    index:
      (props.UserDeviceParams.page - 1) * props.UserDeviceParams.page_size +
      (key + 1),
  }));

  const deviceColumns = [
    {
      title: "S.No",
      dataIndex: "index",
      width: 70,
    },
    {
      title: "Code",
      width: 150,
      render: (row) => <span className={columnClass}>{row.code}</span>,
    },
    {
      title: "Device Name",
      width: 150,
      render: (row) => <span className={columnClass}>{row.name}</span>,
    },
    {
      title: "Device Type",
      width: 150,
      render: (row) => <span className={columnClass}>{row.type_name}</span>,
    },
    {
      title: "Status",
      width: 150,
      render: (row) => (
        <span className={columnClass}>
          {row.is_active ? "active" : "in-active"}
        </span>
      ),
    },
  ];

  const deviceData = props.adminUserDeviceList.map((row, key) => ({
    ...row,
    // key: key + 1,
    // index: key + 1,
    index:
      (props.UserDeviceParams.page - 1) * props.UserDeviceParams.page_size +
      (key + 1),
  }));

  const activityColumns = [
    {
      title: "S.No",
      dataIndex: "index",
      width: 70,
    },
    {
      title: "Date",
      width: 150,
      render: (row) => (
        <span className={columnClass}>
          {moment(row.createdon).format("DD-MM-YYYY, h:mm:ss a")}
        </span>
      ),
    },
    {
      title: "Table Name",
      width: 150,
      render: (row) => <span className={columnClass}>{row.tablename}</span>,
    },
    {
      title: "Type Name",
      width: 150,
      render: (row) => <span className={columnClass}>{row.type_name}</span>,
    },
    {
      title: "User Name",
      width: 150,
      render: (row) => (
        <span className={columnClass}>{row.user?.fullname}</span>
      ),
    },
    {
      title: "Employee Code",
      width: 150,
      render: (row) => (
        <span className={columnClass}>{row.user?.username}</span>
      ),
    },
  ];

  const activityData = props.adminUserActivityList.map((row, key) => ({
    ...row,
    // key: key + 1,
    // index: key + 1,
    index:
      (props.UserDeviceParams.page - 1) * props.UserDeviceParams.page_size +
      (key + 1),
  }));

  return (
    <Row>
      <PageTitle pageTitle="User Details" goBack={true} />

      <Col
        span={24}
        className="hp-mb-16 hp-profile-content-list hp-ml-12 hp-mt-8 hp-pb-sm-0 hp-pb-20"
      >
        <ul>
          <li>
            <span className={listTitle}>User Name</span>
            <span className={listResult}>
              {props.employeeUserdata.user?.username == "" ||
              props.employeeUserdata.user?.username == null
                ? "-------"
                : props.employeeUserdata.user?.username}
            </span>
          </li>

          <li>
            <span className={listTitle}>Full Name</span>
            <span className={listResult}>
              {props.employeeUserdata.user?.username == "" ||
              props.employeeUserdata.user?.username == null
                ? "-------"
                : props.employeeUserdata.user?.first_name}{" "}
              {props.employeeUserdata.user?.last_name}
            </span>
          </li>

          <li className="hp-mt-18">
            <span className={listTitle}>Email</span>
            <a className={listResult} href="mailto:******@gmail.com">
              {props.employeeUserdata.user?.email == "" ||
              props.employeeUserdata.user?.email == null
                ? "-------"
                : props.employeeUserdata.user?.email}
            </a>
          </li>

          <li className="hp-mt-18">
            <span className={listTitle}>Mobile Number</span>
            <a className={listResult} href="tel:+9999999999">
              {props.employeeUserdata.user?.phone == "" ||
              props.employeeUserdata.user?.phone == null
                ? "-------"
                : props.employeeUserdata.user?.phone}
            </a>
          </li>
          <li className="hp-mt-18">
            <span className={listTitle}>Date Of Birth</span>
            {props.employeeUserdata.dob == "" ||
            props.employeeUserdata.dob == null
              ? "-------"
              : moment(props.employeeUserdata.dob)
                  .format("DD-MM-YYYY")
                  .toString()}
          </li>

          {/* <li className="hp-mt-18">
            <span className={listTitle}>Department </span>
            <span className={listResult}>
              {props.employeeUserdata.departments?.name == "" || props.employeeUserdata.departments?.name == null
                ? "-------"
                : props.employeeUserdata.departments?.name}
            </span>
          </li> */}

          <li className="hp-mt-18">
            <span className={listTitle}>Firm </span>
            <span className={listResult}>
              {props.employeeUserdata.firm?.name == "" ||
              props.employeeUserdata.firm?.name == null
                ? "-------"
                : props.employeeUserdata.firm?.name}
            </span>
          </li>

          {/* <li className="hp-mt-18">
            <span className={listTitle}>Profile </span>
            <span className={listResult}>
              {props.employeeUserdata.user?.groups?.name == "" || props.employeeUserdata.user?.groups?.name == null
                ? "-------"
                : props.employeeUserdata.user?.groups?.name}
            </span>
          </li> */}

          <li className="hp-mt-18">
            <span className={listTitle}>State </span>
            <span className={listResult}>
              {props.employeeUserdata.user?.state?.name == "" ||
              props.employeeUserdata.user?.state?.name == null
                ? "-------"
                : props.employeeUserdata.user?.state?.name}
            </span>
          </li>
          <li className="hp-mt-18">
            <span className={listTitle}>District</span>
            <span className={listResult}>
              {props.employeeUserdata.user?.district?.name == "" ||
              props.employeeUserdata.user?.district?.name == null
                ? "-------"
                : props.employeeUserdata.user?.district?.name}
            </span>
          </li>
          <li className="hp-mt-18">
            <span className={listTitle}>City</span>
            <span className={listResult}>
              {props.employeeUserdata.user?.city?.name == "" ||
              props.employeeUserdata.user?.city?.name == null
                ? "-------"
                : props.employeeUserdata.user?.city?.name}
            </span>
          </li>
          <li className="hp-mt-18">
            <span className={listTitle}>Area</span>
            <span className={listResult}>
              {props.employeeUserdata.user?.area?.name == "" ||
              props.employeeUserdata.user?.area?.name == null
                ? "-------"
                : props.employeeUserdata.user?.area?.name}
            </span>
          </li>
          <li className="hp-mt-18">
            <span className={listTitle}>Pincode</span>
            <span className={listResult}>
              {props.employeeUserdata.user?.pincode == "" ||
              props.employeeUserdata.user?.pincode == null
                ? "-------"
                : props.employeeUserdata.user?.pincode}
            </span>
          </li>

          <li className="hp-mt-18">
            <span className={listTitle}>Address</span>
            <span className={listResult}>
              {props.employeeUserdata.user?.address == "" ||
              props.employeeUserdata.user?.address == null
                ? "-------"
                : props.employeeUserdata.user?.address}
            </span>
          </li>
        </ul>
      </Col>
      <Col span={24} className="hp-mt-10">
        <Title level={5}>Counters</Title>
        {props.employeeUserdata.user?.counters?.map((counter) => {
          return (
            <>
              <Text strong keyboard>
                {counter.name}
              </Text>
              {",  "}
            </>
          );
        })}
      </Col>
      <Col span={24} className="hp-mt-10">
        <Title level={5}>Clusters</Title>
        {props.employeeUserdata.user?.clusters?.map((cluster) => {
          return (
            <>
              <Text strong keyboard>
                {cluster.name}
              </Text>
              {",  "}
            </>
          );
        })}
      </Col>

      <Col span={24} className="hp-mt-16">
        <Tabs defaultActiveKey="1" type="card" onChange={callback}>
          <TabPane tab="Logins" key="1">
            <Spin tip="Loading..." spinning={props.loading} delay={500}>
              <Col span={24}>
                <Row className="hp-mb-12">
                  <Col span={24}>
                    <Table
                      className="hp-p-0 hp-mt-10 hp-border-1 hp-border-color-dark-80 "
                      columns={loginColumns}
                      dataSource={loginData}
                      loading={props.loading}
                      pagination={false}
                      size="small"
                    />
                  </Col>

                  <Col span={24}>
                    <Pagination
                      size="small"
                      total={props.loginListCount}
                      showTotal={showTotal}
                      current={props.UserDeviceParams?.page}
                      pageSize={props.UserDeviceParams?.page_size}
                      showSizeChanger
                      onChange={onPageChange}
                      className="hp-mb-12 hp-mt-24 hp-float-right"
                    />
                  </Col>
                </Row>
              </Col>
            </Spin>
          </TabPane>

          <TabPane tab="Devices" key="2">
            <Spin tip="Loading..." spinning={props.loading} delay={500}>
              <Col span={24}>
                <Row className="hp-mb-12">
                  <Col span={24}>
                    <Table
                      className="hp-mt-10 hp-border-1 hp-border-color-dark-80  hp-border-radius"
                      columns={deviceColumns}
                      dataSource={deviceData}
                      loading={props.loading}
                      pagination={false}
                      size="small"
                    />
                  </Col>
                  <Col span={24}>
                    <Pagination
                      size="small"
                      total={props.deviceListCount}
                      showTotal={showTotal}
                      current={props.UserDeviceParams?.page}
                      pageSize={props.UserDeviceParams?.page_size}
                      showSizeChanger
                      onChange={onPageChange}
                      className="hp-mb-12 hp-mt-24 hp-float-right"
                    />
                  </Col>
                </Row>
              </Col>
            </Spin>
          </TabPane>

          <TabPane tab="Activity" key="3">
            <Spin tip="Loading..." spinning={props.loading} delay={500}>
              <Col span={24}>
                <Row className="hp-mb-12">
                  <Col span={24}>
                    <Table
                      className="hp-mt-10 hp-border-1 hp-border-color-dark-80  hp-border-radius"
                      columns={activityColumns}
                      dataSource={activityData}
                      loading={props.loading}
                      pagination={false}
                      size="small"
                    />
                  </Col>
                  <Col span={24}>
                    <Pagination
                      size="small"
                      total={props.activityListCount}
                      showTotal={showTotal}
                      current={props.UserDeviceParams?.page}
                      pageSize={props.UserDeviceParams?.page_size}
                      showSizeChanger
                      onChange={onPageChange}
                      className="hp-mb-12 hp-mt-24 hp-float-right"
                    />
                  </Col>
                </Row>
              </Col>
            </Spin>
          </TabPane>
        </Tabs>
      </Col>
    </Row>
  );
}
function mapStateToProps({ manageUsers, system }) {
  return {
    noofpages: manageUsers.noofpages,
    loginListCount: manageUsers.loginListCount,
    deviceListCount: manageUsers.deviceListCount,
    activityListCount: manageUsers.activityListCount,
    loading: manageUsers.loading,
    adminUserDeviceList: manageUsers.adminUserDeviceList,
    adminUserLoginList: manageUsers.adminUserLoginList,
    UserDeviceParams: manageUsers.UserDeviceParams,
    userAccessList: system.userAccessList,
    activeTab: manageUsers.activeTab,
    adminUserActivityList: manageUsers.adminUserActivityList,
    employeeUserdata: manageUsers.employeeUserdata,
  };
}

export default connect(mapStateToProps, {
  getAdminUserLogin,
  getAdminUserDeviceList,
  setUsersDeviceParams,
  userDeviceActiveTab,
  getAdminUserActivityList,
  clearAdminUserLogin,
  clearAdminUserDeviceList,
  clearAdminUserActivityList,
  getEmployeeUserbyID,
})(UserDetails);
