import React, { useState, useEffect } from "react";
import { Route, Switch } from "react-router-dom";
import { isMobile } from "react-device-detect";
import { connect } from "react-redux";
import { Redirect } from "react-router-dom";
import { Row, Col, Drawer, Button, Dropdown, Menu } from "antd";
import { RiMore2Line, RiMenuFill, RiCloseFill } from "react-icons/ri";

import MenuProfile from "./menu";
import ManageProfile from "./ManageProfile/manage-profile";
import ProfileAdd from "./ManageProfile/profileAdd";
import ManageUsers from "./ManageUsers/manage-users";
import AddUser from "./ManageUsers/addUser";
import LocationManager from "./LocationManager/location-manager";
import Managers from "./Managers/managers";
import Approvals from "./Approval/approval";
import AppSettings from "./app-settings";
import ManageCompany from "./ManageCompany/manage-company";
import ScreenSettings from "./screen-settings";
import BackupDatabase from "./BackupDatabase/backup-database";
import ImportCSV from "./import-csv";
import ExportCSV from "./export-csv";
import ResetDatabase from "./reset-database";
import { sidebarEnable } from "../../../store/customise/customiseSlice";
import {getProfileMenuItems} from "../../../store/system/systemSlice";
import UserDetails from "./ManageUsers/userDetails";


const Profile = (props) => {
  const [visible, setVisible] = useState(false);
  useEffect(() => {
    if (!isMobile) {
      props.sidebarEnable(true);
      if(props.profileMenuItemsList.length == 0){
        props.getProfileMenuItems();
      }
    }
    return () => {
      props.sidebarEnable(false);
    };
  }, []);
  // const { onClose } = props;
  
  const showDrawer = () => {
    setVisible(true);
  };

  const onClose = () => {
    setVisible(false);
  };

  return (
    <Row gutter={[32, 32]} className="hp-mt-32">
      <Drawer
        title=""
        className="hp-profile-mobile-menu"
        placement="left"
        closable={true}
        onClose={onClose}
        visible={visible}
        closeIcon={
          <RiCloseFill className="remix-icon hp-text-color-black-0" size={24} />
        }
      >
        <MenuProfile
          // onCloseDrawer={onClose}
          // onClose={onClose}
          onClose={onClose}
          moreBtnCheck="none"
          footer="none"
        />
      </Drawer>

      <Col span={24}>
        <Row className="hp-profile-mobile-menu-btn hp-isMobile  hp-bg-color-black-0 hp-bg-color-dark-100 hp-border-radius hp-py-12 hp-px-sm-8 hp-px-24 hp-mb-16">
          <Button
            className="hp-p-0"
            type="text"
            icon={
              <RiMenuFill
                size={24}
                className="remix-icon hp-text-color-black-80 hp-text-color-dark-30"
              />
            }
            onClick={showDrawer}
          ></Button>
        </Row>

        <Row className=" hp-bg-color-black-0 hp-bg-color-dark-100 hp-border-radius hp-pr-sm-16 hp-pr-32">
          <MenuProfile />

          <Col
            flex="1 1"
            className="hp-pl-sm-16 hp-pl-32 hp-py-sm-24 hp-py-32 hp-pb-24 hp-overflow-hidden"
          >
            <Switch>
              <Route path="/pages/settings/manage-profile" exact>
                <ManageProfile />
              </Route>
              <Route path="/pages/settings/manage-profile/:id">
                <ProfileAdd />
              </Route>
              <Route path="/pages/settings/manage-users" exact>
                <ManageUsers />
              </Route>
              <Route path="/pages/settings/manage-users/:id">
                <AddUser />
              </Route>
              <Route path="/pages/settings/user-details/:id">
                <UserDetails />
              </Route>
              <Route path="/pages/settings/managers">
                <Managers />
              </Route>
              <Route path="/pages/settings/location-manager">
                <LocationManager />
              </Route>
              <Route path="/pages/settings/approvals">
                <Approvals />
              </Route>

              <Route path="/pages/settings/app-settings">
                <AppSettings />
              </Route>

              <Route path="/pages/settings/manage-company">
                <ManageCompany />
              </Route>
              <Route path="/pages/settings/screen-settings">
                <ScreenSettings />
              </Route>
              <Route path="/pages/settings/backup-database">
                <BackupDatabase />
              </Route>

              <Route path="/pages/settings/import-csv">
                <ImportCSV />
              </Route>
              <Route path="/pages/settings/export-csv">
                <ExportCSV />
              </Route>
              <Route path="/pages/settings/reset-database">
                <ResetDatabase />
              </Route>

              <Route
                path="/pages/settings/"
                // render={() => {
                //     return (
                //       // <Redirect to="/pages/settings/manage-profile" />
                //       <Row className=" hp-text-center">
                //         <Col className="hp-error-content hp-py-32" span={24}>
                //           <Row className="hp-h-100" align="middle" justify="center">
                //             <Col>
                //               {/* <Row gutter={[32, 32]}>{HomeMenus}</Row> */}
                //               <h2>Please Click on Menu</h2>
                //             </Col>
                //           </Row>
                //         </Col>
                //     </Row>
                //     )
                // }}
              >
                <Row className=" hp-text-center">
                  <Col className="hp-error-content hp-py-32" span={24}>
                    <Row className="hp-h-100" align="middle" justify="center">
                      <Col>
                        <h2>Please Click on Menu</h2>
                      </Col>
                    </Row>
                  </Col>
                </Row>
              </Route>

            </Switch>
          </Col>
        </Row>
      </Col>
    </Row>
  );
};
function mapStateToProps({ system }) {
  return {
    profileMenuItemsList: system.profileMenuItemsList,
  };
}

export default connect(mapStateToProps, {
  sidebarEnable,
  getProfileMenuItems
})(Profile);
