import React, { useEffect, useState } from "react";

import {
  Table,
  Divider,
  Button,
  Input,
  Row,
  Col,
  Space,
  Drawer,
  Popconfirm,
  Pagination,
} from "antd";
import {
  EyeOutlined,
  FilterOutlined,
  UploadOutlined,
  UserOutlined,
} from "@ant-design/icons";
import { connect } from "react-redux";
import { RiErrorWarningLine } from "react-icons/ri";
import { isMobile } from "react-device-detect";
import {
  getManagers,
  isModelVisible,
  managerbyId,
  managerDelete,
  setManagersParams,
} from "../../../../store/settings/Managers/ManagersSlice";

import FilterMenu from "../filter/FilterMenu";
import AddManager from "./addManager";

function Managers(props) {
  const [visible, setVisible] = useState(false);
  const { Search } = Input;
  useEffect(() => {
    props.getManagers({ page: 1, search: "", page_size: 10 });
  }, []);

  const columnClass =
    "hp-p1-body hp-text-color-black-100 hp-text-color-dark-0 hp-font-weight-600 ";
  const dividerClass = "hp-border-color-black-40 hp-border-color-dark-80";
  const columns = [
    {
      title: "S.No",
      width: "5%",
      render: (row) => <span className={columnClass}>{row.index}</span>,
    },
    {
      title: "Code",
      width: "15%",
      render: (row) => <span className={columnClass}>{row.code}</span>,
    },
    {
      title: "Name",
      width: "15%",
      render: (row) => <span className={columnClass}>{row.name}</span>,
    },

    {
      title: "Actions",
      key: "action",
      sorter: true,
      width: "10%",
      render: (row) => (
        <Space size="middle">
          {props.userAccessList?.indexOf("Permissions.change_manager") !== -1 && (
          <i
            className="ri-edit-box-line hp-mr-2 "
            style={{ cursor: "pointer", color: "#fc6f03" }}
            onClick={() => {
              props.managerbyId(row.id);
              props.isModelVisible(true);
            }}
          ></i>
          )}

        {props.userAccessList?.indexOf("Permissions.delete_manager") !== -1 && (
          <Popconfirm
            placement="topRight"
            title="Are you sure to delete this Record?"
            onConfirm={() => props.managerDelete(row.id)}
            okText="Yes"
            cancelText="No"
            className="hp-ml-8"
            icon={
              <RiErrorWarningLine className="remix-icon hp-text-color-primary-1" />
            }
          >
            <i
              className="ri-delete-bin-2-line "
              style={{ cursor: "pointer", color: "#ff0000" }}
            ></i>
          </Popconfirm>
        )}
        </Space>
      ),
    },
  ];

  const data = props.managerslist.map((row, key) => ({
    ...row,
    index:
      (props.managesParams.page - 1) * props.managesParams.page_size +
      (key + 1),
  }));

  // const [openFilter, setOpenFilter] = useState(false);

  const showDrawer = () => {
    setVisible(true);
  };

  const onClose = () => {
    setVisible(false);
  };

  const openFilter = () => {
    // debugger
    setVisible(true);
  };
  function onPageChange(page, pageSize) {
    props.getManagers({
      ...props.managesParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
    props.setManagersParams({
      ...props.managesParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  function onSearch(val) {
    props.getManagers({
      ...props.managesParams,
      page: 1,
      search: val,
    });
    props.setManagersParams({
      ...props.managesParams,
      page: 1,
      search: val,
    });
  }
  function showTotal(total) {
    return "Total " + total + " items";
  }
  function onSearch(val) {
    props.getManagers({
      ...props.managesParams,
      page: 1,
      search: val,
      page_size: 10,
    });
    props.setManagersParams({
      ...props.managesParams,
      page: 1,
      search: val,
      page_size: 10,
    });
  }

  return (
    <div className="openFilter">
      <div className="hp-profile-activity">
        <Row className="hp-mb-16">
          <Col md={6} span={24} sm={12} xs={12} className="hp-pr-sm-0 hp-pr-12">
            <h4 className="hp-mt-8">Managers</h4>
          </Col>

          {/* <Col md={6} span={24} sm={12} xs={12} className="hp-pr-sm-0 hp-pr-12">
            <Button
              block
              type="primary"
              htmlType="submit"
              onClick={() => props.isModelVisible(true)}
            >
              Add Manager
            </Button>
          </Col> */}
        </Row>

        <Row gutter={[12, 12]}>
          <Col  span={24} xs={24} sm={24} md={11} lg={11} className="hp-mr-10 hp-mb-xs-10">
            <Search
              placeholder="Search for date, doc, number, customer, counter, vehicle"
              allowClear
              onChange={(e) => {
                if (e.target.value == "") {
                  props.getManagers({
                    ...props.managesParams,
                    page: 1,
                    search: "",
                  });
                  props.setManagersParams({
                    ...props.managesParams,
                    page: 1,
                    search: "",
                  });
                }
              }}
              onSearch={(value) => {
                onSearch(value);
              }}
            />
          </Col>

          {/* <Col span={24} xs={6} sm={6} md={4} lg={4}>
            <Drawer
              placement="right"
              width={300}
              closable={false}
              onClose={onClose}
              visible={visible}
              className="hp-drawer-mobile hp-pl-0 hp-pr-0"
            >
              <FilterMenu visible={visible} setVisible={setVisible} />
            </Drawer>

            <Button
              className="hp-w-100 hp-p-10"
              onClick={showDrawer}
              icon={<FilterOutlined className="hp-mr-10" />}
            >
              {!isMobile && "Filter"}
            </Button>
          </Col> */}

          {/* <Col span={24} xs={6} sm={6} md={4} lg={4}>
            <Button
              className="hp-w-100 hp-p-10"
              icon={<UploadOutlined className="hp-mr-10" />}
            >
              {!isMobile && "Export"}
            </Button>
          </Col> */}

          <Col span={24} xs={12} sm={12} md={4} lg={4}>
            {props.userAccessList?.indexOf("Permissions.add_manager") !== -1 && (
              <Button
                block
                type="primary"
                htmlType="submit"
                onClick={() => props.isModelVisible(true)}
              >
                Add Manager
              </Button>
            )}
          </Col>
        </Row>

        <Divider className={dividerClass} />

        <Table
          className="hp-overflow-scroll hp-border-1 hp-border-color-dark-80 hp-border-bottom-none hp-border-radius"
          columns={columns}
          dataSource={data}
          // rowSelection={{
          //   type: "checkbox",
          //   hideSelectAll: true,
          // }}
          pagination={false}
          size="middle"
        />

        <Pagination
          size="small"
          current={props.managesParams?.page}
          pageSize={props.managesParams?.page_size}
          total={props.listCount}
          showTotal={showTotal}
          showSizeChanger
          // onShowSizeChange={onShowSizeChange}
          onChange={onPageChange}
          on
          className="hp-mb-12 hp-mt-24 hp-float-right"
        />
      </div>
      <AddManager />
    </div>
  );
}
function mapStateToProps({ managers, system }) {
  return {
    managerslist: managers.managerslist,
    noofpages: managers.noofpages,
    listCount: managers.listCount,
    managesParams: managers.managesParams,
    userAccessList: system.userAccessList,
  };
}

export default connect(mapStateToProps, {
  getManagers,
  managerbyId,
  managerDelete,
  isModelVisible,
  setManagersParams,
})(Managers);
