import { Fragment, useState, useEffect } from "react";

import { Link, useHistory } from "react-router-dom";
import { connect,  } from "react-redux";
import moment from "moment";
import "flatpickr/dist/themes/light.css";
import { RiCloseFill } from "react-icons/ri";
import {
  Button,
  Input,
  Form,
  Modal,
  Badge,
  Row,
  Col,
  Select,
  DatePicker,
} from "antd";

import {
  isPasswordModel,
  InputChangeValue,
  verifyPassword
} from "../../../../store/settings/BackupDatabse/backupDataBaseSlice";

const BackupPassword = (props) => {
  let history = useHistory()

  const [form] = Form.useForm();
  function ChangeHandeler(e) {
    props.InputChangeValue({
      key: e.target.name,
      value: e.target.value,
    });
  }
console.log("password", props.backupData);
  const handleUpdate = () => {
    const obj = {
      password: props.backupData.password,
    };
    // console.log("props.dataBaseFile", props.dataBaseFile);
    props.verifyPassword({obj, dataBaseId: props.dataBaseId});
  };

  // Reset Input Values on Close
  const handleResetInputValues = () => {
    props.isPasswordModel(false);
  };

  const FooterAddOrUpdate = () => {
    return (
      <Fragment>
        <Button
          type="primary"
          onClick={() => {
            form.submit();
          }}
          block
          loading={props.verifyPwdloading}
          disabled = {props.verifyPwdloading}
        >
          Verify
        </Button>
        {/* <Button
          type="primary"
          onClick={() => {
            form.submit();
          }}
          block
          loading={props.verifyPwdloading}
          disabled = {props.verifyPwdloading}
        >
          Download
        </Button> */}
      </Fragment>
    );
  };

  const FooterCancel = () => {
    return (
      <Fragment>
        <Button
          onClick={() => {
            handleResetInputValues();
          }}
          type="primary"
          block
          ghost
        >
          Cancel
        </Button>
      </Fragment>
    );
  };

  const modalTitle = <h5 className="modal-title">Verify Password </h5>;

  useEffect(() => {
    form.setFieldsValue({
      password: props.backupData.password,
    });
  }, [props.backupData]);

  return (
    <Modal
      visible={props.passwordModel}
      title={modalTitle}
      onCancel={() => {
        handleResetInputValues();
        props.isPasswordModel(false);
      }}
      className="hp-modal-p-24"
      footer={
        <Row justify="center">
          <Col span={12} className="hp-pr-4">
            <FooterCancel />
          </Col>

          <Col span={12} className="hp-pl-4">
            <FooterAddOrUpdate />
          </Col>
        </Row>
      }
      centered
      closeIcon={
        <RiCloseFill className="remix-icon text-color-black-100" size={24} />
      }
      width={516}
    >
      <Form
        layout="vertical"
        form={form}
        onFinish={() => {
          handleUpdate();
        }}
        initialValues={{ remember: true }}
      >
        <Row gutter={[12, 0]}>
          <Col span={24} xs={24} xl={24} md={24}>
            <Form.Item
              label="Password :"
              name="password"
              rules={[
                {
                  required: true,
                  message: "Please Input password",
                },
              ]}
              hasFeedback
            >
              <Input.Password
                id="password"
                name="password"
                style={{ width: "100%" }}
                placeholder="Password"
                value={
                  props.backupData.password == null
                    ? ""
                    : props.backupData?.password
                }
                onChange={ChangeHandeler}
              />
            </Form.Item>
          </Col>
        </Row>
      </Form>
    </Modal>
  );
};

function mapStateToProps({ backupDatabase }) {
  return {
    passwordModel: backupDatabase.passwordModel,
    backupData: backupDatabase.backupData,
    dataBaseId: backupDatabase.dataBaseId,
    verifyPwdloading: backupDatabase.verifyPwdloading
  };
}

export default connect(mapStateToProps, {
  isPasswordModel,
  InputChangeValue,
  verifyPassword,
})(BackupPassword);
