import { Fragment, useState, useEffect } from "react";

import { connect } from "react-redux";
import moment from "moment";
import "flatpickr/dist/themes/light.css";
import { RiCloseFill, RiInformationLine } from "react-icons/ri";
import {
  Button,
  Input,
  Form,
  Modal,
  Badge,
  Row,
  Col,
  Select,
  Switch,
} from "antd";
import {
  // countryAdd,
  isModelVisible,
  InputChangeValue,
  approvalAdd,
} from "../../../../store/settings/Approval/approvalSlice";
import NumericInput from "../../../components/data-entry/inputs/numberInput";

import { getMiniManagers } from "../../../../store/mini/miniSlice";

export const isObjEmpty = (obj) => Object.keys(obj).length === 0;
const AddApproval = (props) => {
  const [form] = Form.useForm();
  const { Option } = Select;
  useEffect(() => {
    props.getMiniManagers();
  }, []);
  console.log("props.approvalData", props.approvalData);

  function ChangeHandeler(e) {
    props.InputChangeValue({
      key: e.target.name,
      value: e.target.value,
    });
  }
  const locationTypeList = [
    { id: 1, name: "Counter" },
    { id: 2, name: "Cluster" },
    { id: 3, name: "Division" },
    { id: 4, name: "Zone" },
  ];
  const transtiontypeList = [
    { id: 1, name: "Purchase" },
    { id: 2, name: "Sales" },
  ];

  const handleAdd = () => {
    const obj = {
      id: 0,
      ...props.approvalData,
    };
    props.approvalAdd(obj);
  };

  const handleUpdate = () => {
    const obj = {
      id: props.approvalData.id,
      ...props.approvalData,
    };
    props.approvalAdd(obj);
  };

  // Reset Input Values on Close
  const handleResetInputValues = () => {
    props.isModelVisible(false);
    form.resetFields();
  };

  const FooterAddOrUpdate = () => {
    if (props.approvalData.id == null || props.approvalData.id == 0) {
      return (
        <Fragment>
          <Button
            onClick={() => {
              form.submit();
            }}
            type="primary"
            block
          >
            Add
          </Button>
        </Fragment>
      );
    } else {
      return (
        <Fragment>
          <Button
            type="primary"
            onClick={() => {
              form.submit();
            }}
            block
          >
            Update
          </Button>
        </Fragment>
      );
    }
  };

  const FooterCancel = () => {
    return (
      <Fragment>
        <Button
          onClick={() => {
            handleResetInputValues();
          }}
          type="primary"
          block
          ghost
        >
          Cancel
        </Button>
      </Fragment>
    );
  };


  

  const modalTitle = (
    <h5 className="modal-title">
      {props.approvalData.id == null || props.approvalData.id == 0
        ? "Add"
        : "Update"}{" "}
      Approval
    </h5>
  );
  useEffect(() => {
    form.setFieldsValue({
      levelno: props.approvalData.levelno,
      manager_id: props.approvalData.manager_id,
      locationtype: props.approvalData.locationtype,
      transtiontype: props.approvalData.transtiontype,
      finalapproval: props.approvalData.finalapproval,
      fieldverfication: props.approvalData.fieldverfication,
    });
  }, [props.approvalData]);

  return (
    <Modal
      visible={props.model}
      title={modalTitle}
      onCancel={() => {
        handleResetInputValues();
        props.isModelVisible(false);
        form.resetFields();
      }}
      className="hp-modal-p-24"
      footer={
        <Row justify="center">
          <Col span={12} className="hp-pr-4">
            <FooterCancel />
          </Col>

          <Col span={12} className="hp-pl-4">
            <FooterAddOrUpdate />
          </Col>
        </Row>
      }
      centered
      closeIcon={
        <RiCloseFill className="remix-icon text-color-black-100" size={24} />
      }
      width={716}
    >
      <Form
        layout="vertical"
        form={form}
        onFinish={() => {
          if (props.approvalData.id == null || props.approvalData.id == 0) {
            handleAdd();
          } else {
            handleUpdate();
          }
        }}
        initialValues={{ remember: true }}
      >
        <Row gutter={[12, 0]}>
          <Col span={24} xs={24} xl={12} md={12}>
            <Form.Item
              label="Level No"
              name="levelno"
              rules={[
                {
                  required: true,
                  message: "Please Enter Level No",
                },
              ]}
              getValueProps={() => {}}
              hasFeedback
            >
              <NumericInput
                id="levelno"
                name="levelno"
                style={{ width: "100%" }}
                placeholder="Level No"
                maxLength={6}
                value={
                  props.approvalData.levelno == null
                    ? ""
                    : props.approvalData?.levelno
                }
                onChange={props.InputChangeValue}
              />
            </Form.Item>
          </Col>
          <Col span={24} xs={24} xl={12} md={12}>
            <Form.Item
              label="Manager"
              name="manager_id"
              rules={[
                {
                  required: true,
                  message: "Please Select Manager",
                },
              ]}
              hasFeedback
            >
              <Select
                showSearch
                placeholder="Select Manager"
                optionFilterProp="Manager"
                // disabled
                filterOption={(input, option) =>
                  option.children.toLowerCase().includes(input.toLowerCase())
                }
                onChange={(value) => {
                  props.InputChangeValue({
                    key: "manager_id",
                    value: value,
                  });
                }}
                value={
                  props.approvalData.manager_id == null
                    ? ""
                    : props.approvalData?.manager_id
                }
              >
                {props.miniManagersList.map((manage, index) => {
                  return (
                    <Option key={index} value={manage.id}>
                      {manage.name}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Col>
          <Col span={24} xs={24} xl={12} md={12}>
            <Form.Item
              label="Location Type"
              name="locationtype"
              rules={[
                {
                  required: true,
                  message: "Please Select Location Type",
                },
              ]}
              hasFeedback
            >
              <Select
                showSearch
                placeholder="Select Location Type"
                optionFilterProp="locationtype"
                // disabled
                filterOption={(input, option) =>
                  option.children.toLowerCase().includes(input.toLowerCase())
                }
                onChange={(value) => {
                  props.InputChangeValue({
                    key: "locationtype",
                    value: value,
                  });
                }}
                value={
                  props.approvalData.locationtype == null
                    ? ""
                    : props.approvalData?.locationtype
                }
              >
                {locationTypeList.map((locationType, index) => {
                  return (
                    <Option key={index} value={locationType.id}>
                      {locationType.name}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Col>
          <Col span={24} xs={24} xl={12} md={12}>
            <Form.Item
              label="Transtion Type"
              name="transtiontype"
              rules={[
                {
                  required: true,
                  message: "Please Select Transtion Type",
                },
              ]}
              hasFeedback
            >
              <Select
                showSearch
                placeholder="Select Transtion Type"
                optionFilterProp="transtiontype"
                // disabled
                filterOption={(input, option) =>
                  option.children.toLowerCase().includes(input.toLowerCase())
                }
                onChange={(value) => {
                  props.InputChangeValue({
                    key: "transtiontype",
                    value: value,
                  });
                }}
                value={
                  props.approvalData.transtiontype == null
                    ? ""
                    : props.approvalData?.transtiontype
                }
              >
                {transtiontypeList.map((transtionType, index) => {
                  return (
                    <Option key={index} value={transtionType.id}>
                      {transtionType.name}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Col>
          <Col span={24} xs={24} xl={6} md={6}>
            <Form.Item
              label="Final Approval"
              name="finalapproval"
              rules={[
                {
                  required: true,
                  message: "Please Select final approval",
                },
              ]}
              hasFeedback
            >
              {" "}
              <Switch
                checkedChildren="On"
                unCheckedChildren="Off"
                // defaultChecked finalapproval
                className="hp-mb-16"
                checked={props.approvalData.finalapproval}
                onChange={(value) => {
                  props.InputChangeValue({
                    key: "finalapproval",
                    value: value,
                  });
                }}
              />
            </Form.Item>
          </Col>

          <Col span={24} xs={24} xl={6} md={6}>
            <Form.Item
              label="Field Verification"
              name="fieldverfication"
              // rules={[
              //   {
              //     required: true,
              //     message: "Please Select final approval",
              //   },
              // ]}
              hasFeedback
            >
              {" "}
              <Switch
                checkedChildren="On"
                unCheckedChildren="Off"
                // defaultChecked finalapproval
                className="hp-mb-16"
                checked={props.approvalData.fieldverfication}
                onChange={(value) => {
                  props.InputChangeValue({
                    key: "fieldverfication",
                    value: value,
                  });
                }}
              />
            </Form.Item>
          </Col>
        </Row>
      </Form>
    </Modal>
  );
};

function mapStateToProps({ approval, mini,  }) {
  return {
    approvalData: approval.approvalData,
    model: approval.model,
   
    miniManagersList: mini.miniManagersList,
  };
}

export default connect(mapStateToProps, {
  isModelVisible,
  InputChangeValue,
  getMiniManagers,

  approvalAdd,
})(AddApproval);
