import { Fragment, useState, useEffect } from "react";

import { connect } from "react-redux";
import moment from "moment";
import "flatpickr/dist/themes/light.css";
import { RiCloseFill } from "react-icons/ri";
import {
  Button,
  Input,
  Form,
  Modal,
  Badge,
  Row,
  Col,
  Select,
  DatePicker,
} from "antd";
import {
  // countryAdd,
  isModelVisible,
  InputChangeValue,
  locationManagerAdd,
} from "../../../../store/settings/LocationManager/locationManagerSlice";
import {
  getUsers,
  setUsersParams,
} from "../../../../store/settings/ManageUsers/manageUserSlice";
import {
  getMiniManagers,
  getMiniCounters,
  getMiniClusters,
  getMiniDivisions,
  getMiniZones,
} from "../../../../store/mini/miniSlice";

export const isObjEmpty = (obj) => Object.keys(obj).length === 0;
const AddLocationManager = (props) => {
  const [form] = Form.useForm();
  const { Option } = Select;
  useEffect(() => {
    props.getUsers();
    props.getMiniManagers();
    props.getMiniCounters();
    props.getMiniClusters();
    props.getMiniDivisions();
    props.getMiniZones();
  }, []);
  console.log("props.locationManagerData", props.locationManagerData);

  function ChangeHandeler(e) {
    props.InputChangeValue({
      key: e.target.name,
      value: e.target.value,
    });
  }
  const locationTypeList = [
    { id: 1, name: "Counter" },
    { id: 2, name: "Cluster" },
    { id: 3, name: "Division" },
    { id: 4, name: "Zone" },
  ];

  const handleAdd = () => {
    const obj = {
      id: 0,
      ...props.locationManagerData,
    };
    props.locationManagerAdd(obj);
  };

  const handleUpdate = () => {
    const obj = {
      id: props.locationManagerData.id,
      ...props.locationManagerData,
    };
    props.locationManagerAdd(obj);
  };

  // Reset Input Values on Close
  const handleResetInputValues = () => {
    props.isModelVisible(false);
  };

  const FooterAddOrUpdate = () => {
    if (
      props.locationManagerData.id == null ||
      props.locationManagerData.id == 0
    ) {
      return (
        <Fragment>
          <Button
            onClick={() => {
              handleAdd();
            }}
            type="primary"
            block
          >
            Add
          </Button>
        </Fragment>
      );
    } else {
      return (
        <Fragment>
          <Button
            type="primary"
            onClick={() => {
              handleUpdate();
            }}
            block
          >
            Update
          </Button>
        </Fragment>
      );
    }
  };

  const FooterCancel = () => {
    return (
      <Fragment>
        <Button
          onClick={() => {
            handleResetInputValues();
          }}
          type="primary"
          block
          ghost
        >
          Cancel
        </Button>
      </Fragment>
    );
  };

  const modalTitle = (
    <h5 className="modal-title">
      {props.locationManagerData.id == null || props.locationManagerData.id == 0
        ? "Add"
        : "Update"}{" "}
      Location Manager
    </h5>
  );
  useEffect(() => {
    form.setFieldsValue({
      user_id: props.locationManagerData.user_id,
      manager_id: props.locationManagerData.manager_id,
      locationtype: props.locationManagerData.locationtype,
      location_id: props.locationManagerData.location_id,
    });
  }, [props.locationManagerData]);

  return (
    <Modal
      visible={props.model}
      title={modalTitle}
      onCancel={() => {
        handleResetInputValues();
        props.isModelVisible(false);
      }}
      className="hp-modal-p-24"
      footer={
        <Row justify="center">
          <Col span={12} className="hp-pr-4">
            <FooterCancel />
          </Col>

          <Col span={12} className="hp-pl-4">
            <FooterAddOrUpdate />
          </Col>
        </Row>
      }
      centered
      closeIcon={
        <RiCloseFill className="remix-icon text-color-black-100" size={24} />
      }
      width={716}
    >
      <Form
        layout="vertical"
        form={form}
        onFinish={() => {
          if (props.locationManagerData.id == null) {
            handleAdd();
          } else {
            handleUpdate();
          }
        }}
        initialValues={{ remember: true }}
      >
        <Row gutter={[12, 0]}>
          
          <Col span={24} xs={24} xl={12} md={12}>
            <Form.Item
              label="Location Type"
              name="locationtype"
              rules={[
                {
                  required: true,
                  message: "Please Select Location Type",
                },
              ]}
              hasFeedback
            >
              <Select
                showSearch
                placeholder="Select Location Type"
                optionFilterProp="locationtype"
                // disabled
                filterOption={(input, option) =>
                  option.children.toLowerCase().includes(input.toLowerCase())
                }
                onChange={(value) => {
                  props.InputChangeValue({
                    key: "locationtype",
                    value: value,
                  });
                }}
                value={
                  props.locationManagerData.locationtype == null
                    ? ""
                    : props.locationManagerData?.locationtype
                }
              >
                {locationTypeList.map((locationType, index) => {
                  return (
                    <Option key={index} value={locationType.id}>
                      {locationType.name}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Col>
          <Col span={24} xs={24} xl={12} md={12}>
            <Form.Item
              label="Location"
              name="location_id"
              rules={[
                {
                  required: true,
                  message: "Please Select Location",
                },
              ]}
              hasFeedback
            >
              <Select
                showSearch
                placeholder="Select Location"
                optionFilterProp="location_id"
                // disabled
                filterOption={(input, option) =>
                  option.children.toLowerCase().includes(input.toLowerCase())
                }
                onChange={(value) => {
                  props.InputChangeValue({
                    key: "location_id",
                    value: value,
                  });
                }}
                value={
                  props.locationManagerData.location_id == null
                    ? ""
                    : props.locationManagerData?.location_id
                }
              >
                {props.locationManagerData?.locationtype == 1 && (
                  <>
                    {props.miniCountersList.map((location, index) => {
                      return (
                        <Option key={index} value={location.id}>
                          {location.name}
                        </Option>
                      );
                    })}
                  </>
                )}
                {props.locationManagerData?.locationtype == 2 && (
                  <>
                    {props.miniClustersList.map((location, index) => {
                      return (
                        <Option key={index} value={location.id}>
                          {location.name}
                        </Option>
                      );
                    })}
                  </>
                )}
                {props.locationManagerData?.locationtype == 3 && (
                  <>
                    {props.miniDivisionsList.map((location, index) => {
                      return (
                        <Option key={index} value={location.id}>
                          {location.name}
                        </Option>
                      );
                    })}
                  </>
                )}

                {props.locationManagerData?.locationtype == 4 && (
                  <>
                    {props.miniZonesList.map((location, index) => {
                      return (
                        <Option key={index} value={location.id}>
                          {location.name}
                        </Option>
                      );
                    })}
                  </>
                )}
              </Select>
            </Form.Item>
          </Col>
          <Col span={24} xs={24} xl={12} md={12}>
            <Form.Item
              label="Manager"
              name="manager_id"
              rules={[
                {
                  required: true,
                  message: "Please Select Manager",
                },
              ]}
              hasFeedback
            >
              <Select
                showSearch
                placeholder="Select Manager"
                optionFilterProp="Manager"
                // disabled
                filterOption={(input, option) =>
                  option.children.toLowerCase().includes(input.toLowerCase())
                }
                onChange={(value) => {
                  props.InputChangeValue({
                    key: "manager_id",
                    value: value,
                  });
                }}
                value={
                  props.locationManagerData.manager_id == null
                    ? ""
                    : props.locationManagerData?.manager_id
                }
              >
                {props.miniManagersList.map((manage, index) => {
                  return (
                    <Option key={index} value={manage.id}>
                      {manage.name}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Col>
          <Col span={24} xs={24} xl={12} md={12}>
            <Form.Item
              label="User"
              name="user_id"
              rules={[
                {
                  required: true,
                  message: "Please Select User",
                },
              ]}
              hasFeedback
            >
              <Select
                showSearch
                placeholder="Select User"
                optionFilterProp="User"
                // disabled
                filterOption={(input, option) =>
                  option.children.toLowerCase().includes(input.toLowerCase())
                }
                onSearch={(value) => {
                  props.setUsersParams({
                    ...props.usersParams,
                    page: 1,
                    search: value,
                  });
                  props.getUsers({
                    ...props.usersParams,
                    page: 1,
                    search: value,
                  });
                }}
                onChange={(value) => {
                  props.InputChangeValue({
                    key: "user_id",
                    value: value,
                  });
                }}
                onPopupScroll={() => {
                  if (props.usersParams.page < props.usersParams.no_of_pages) {
                    props.getUsers({
                      ...props.usersParams,
                      page: props.usersParams.page + 1,
                    });
                    props.setUsersParams({
                      ...props.usersParams,
                      page: props.usersParams.page + 1,
                    });
                  }
                }}
                value={
                  props.locationManagerData.user_id == null
                    ? ""
                    : props.locationManagerData?.user_id
                }
              >
                {props.Userslist.map((empUser, index) => {
                  return (
                    <Option key={index} value={empUser.user.id}>
                      {empUser.user.username}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Col>
          
        </Row>
      </Form>
    </Modal>
  );
};

function mapStateToProps({ locationManager, manageUsers, mini }) {
  return {
    locationManagerData: locationManager.locationManagerData,
    model: locationManager.model,
    Userslist: manageUsers.Userslist,
    usersParams: manageUsers.usersParams,
    miniManagersList: mini.miniManagersList,
    miniCountersList: mini.miniCountersList,
    miniClustersList: mini.miniClustersList,
    miniDivisionsList: mini.miniDivisionsList,
    miniZonesList: mini.miniZonesList,
  };
}

export default connect(mapStateToProps, {
  getUsers,
  setUsersParams,
  isModelVisible,
  InputChangeValue,
  getMiniManagers,
  getMiniCounters,
  getMiniClusters,
  getMiniDivisions,
  getMiniZones,
  locationManagerAdd,
})(AddLocationManager);
