import { React, Fragment, useState, useEffect } from "react";

import { connect } from "react-redux";

import {
  Row,
  Col,
  Divider,
  Steps,
  Card,
  Upload,
  Form,
  Input,
  Button,
  message,
  Typography,
  Select,
  List,
  Table,
  Result,
  Skeleton,
  Tag,
  Popover,
  Modal,
  Alert,
} from "antd";
import {
  LoadingOutlined,
  SmileOutlined,
  SolutionOutlined,
  UserOutlined,
  EyeOutlined,
  CloudUploadOutlined,
  InboxOutlined,
} from "@ant-design/icons";
import {
  RiDownload2Fill,
  RiCheckboxCircleFill,
  RiCloseCircleFill,
  RiAlertLine,
  RiInformationLine,
  RiCloseCircleLine,
} from "react-icons/ri";

import {
  getImportModelsList,
  InputChangeValue,
  uploadImportData,
  uploadImportType2Data,
  nextStep,
  prevStep,
  submitImportData,
  submitImportType2Data,
  finishStep,
  changeType,
  clearData
} from "../../../store/settings/ImportExport/importExportSlice";
import { getImportMenuItems } from "../../../store/system/systemSlice";
const { Step } = Steps;
const { confirm } = Modal;
const { Dragger } = Upload;
const { Paragraph, Title, Text, Link } = Typography;
const ImportCSV = (props) => {
  const dividerClass = "hp-border-color-black-40 hp-border-color-dark-80";
  const [form] = Form.useForm();
  useEffect(() => {
    // props.getImportModelsList();
    props.clearData();
    props.getImportMenuItems();
  }, []);

  const steps = [
    {
      title: "Upload file",
      icon: <CloudUploadOutlined />,
    },
    {
      title: "Preview",
      icon: <SolutionOutlined />,
    },
    {
      title: "Finish",
      icon: <SmileOutlined />,
    },
  ];

  const data = [
    "Reaching the targets and goals set for my area.",
    "Servicing the needs of my existing customers.",
    "Maintaining the relationships with existing customers for repeat business.",
    "Reporting to top managers",
    "Keeping up to date with the products.",
  ];
  var getsubval = (keys, row) => {
    var k = keys.shift();
    if (keys.length == 0) {
      return row[k];
    } else {
      return getsubval(keys, row[k]);
    }
  };
  var getObjValue = (key, row) => {
    if (/__/.test(key)) {
      var keys = key.split("__");
      return getsubval(keys, row);
    } else {
      return row[key];
    }
  };
  const columns = props.importTableColumns.map((column) => {
    if (props.importType == 1) {
      if (
        !props.importData.has_errors &&
        !props.importData.has_validation_errors
      ) {
        return {
          title: column,
          render: (row) => {
            return (
              <div dangerouslySetInnerHTML={{ __html: row[column] }}></div>
            );
          },
        };
      } else {
        if (column != "errors") {
          return {
            title: column,
            render: (row) => {
              return <spa>{row[column]}</spa>;
            },
          };
        } else {
          return {
            title: "errors",
            width: 200,
            render: (row) => (
              <div>
                <Popover
                  content={errorsContent(row.errors.error_list)}
                  placement="bottom"
                  overlayStyle={{
                    width: "15vw",
                  }}
                >
                  <Tag color="red">{row.errors.error_count}</Tag>
                </Popover>
              </div>
            ),
          };
        }
      }
    } else if (props.importType == 2) {
      if (column.key == "import_status") {
        return {
          title: column.label,
          render: (row) => {
            return <spa>{row[column.key]}</spa>;
          },
        };
      } else if (column.key == "errors") {
        return {
          title: column.label,
          render: (row) => {
            return (
              <div>
                <Popover
                  content={errorsContent(row.errors)}
                  placement="bottom"
                  overlayStyle={{
                    width: "15vw",
                  }}
                >
                  <Tag color={row.has_errors ? "red" : "green"}>
                    {row.has_errors ? "Yes" : "No"}
                  </Tag>
                </Popover>
              </div>
            );
          },
        };
      } else {
        return {
          title: column.label,
          render: (row) => {
            if (row.import_status == "New") {
              var value2 = getObjValue(column.key, row.output_data);
              return (
                <>
                  <Text strong>{value2}</Text>
                </>
              );
            } else {
              var value1 = getObjValue(column.key, row.validated_data);
              var value2 = getObjValue(column.key, row.output_data);
              if (value1 === value2) {
                return (
                  <>
                    <Text strong>{value1}</Text>
                  </>
                );
              } else {
                return (
                  <>
                    <Text strong type="success">
                      {value1}
                    </Text>
                    <Text delete type="danger">
                      {value2}
                    </Text>
                  </>
                );
              }
            }
          },
        };
      }
    }
  });
  function errorsContent(errorData) {
    var keys = Object.keys(errorData);

    for (let i = 0; i < keys.length; i++) {
      const key = keys[i];

      if (errorData.hasOwnProperty(key)) {
        return (
          <>
            <List
              header={
                <Text type="danger" strong>
                  {key.toUpperCase()}
                </Text>
              }
              itemLayout="horizontal"
              dataSource={errorData[key]}
              renderItem={(item, key) => <List.Item>{item}</List.Item>}
            />
          </>
        );
      }
    }
  }

  const tableData = props.importTableRowData?.map((row, key) => ({
    ...row,
  }));

  function checkImportData() {
    var obj = {
      model_name: props.exportData.model_name,
      import_file: props.exportData.import_file.file,
      input_format: 0,
    };
    confirm({
      title: <h5 className="hp-mb-0 hp-font-weight-500">Are you sure?</h5>,
      icon: (
        <span className="remix-icon">
          <RiInformationLine />
        </span>
      ),
      content: (
        <p className="hp-p1-body hp-text-color-black-80">
          Before Uploading check your file and file type.{" "}
        </p>
      ),
      onOk() {
        if (props.importType == 1) {
          props.uploadImportData(obj);
        } else if (props.importType == 2) {
          props.uploadImportType2Data(obj);
        }
      },
      onCancel() {},
    });
  }

  function submitData() {
    if (props.importType == 1) {
      props.submitImportData({
        ...props.importData.initial,
        model_name: props.exportData.model_name,
      });
    } else if (props.importType == 2) {
      props.submitImportType2Data({
        ...props.importData.initial,
        model_name: props.exportData.model_name,
      });
    }
    props.nextStep();
  }
  function checkMenuType(value) {
    props.importMenuItemsList?.menuitems?.map((menuItem, index) => {
      if (menuItem.link == value) {
        props.changeType(menuItem.click);
      }
    });
  }
  useEffect(() => {
    form.setFieldsValue({
      model_name: props.exportData.model_name,
      import_file: props.exportData.import_file,
    });
  }, [props.exportData]);
  return (
    <Row>
      <Col span={24}>
        <Row className="hp-mb-16">
          <Col span={16} xs={16} sm={16} md={16}>
            <h4 className="hp-mt-8">Import CSV</h4>
          </Col>
          <Col span={8} xs={8} sm={8} md={8}>
            <div className="steps-action">
              {props.currentStep < steps.length - 1 && (
                <Button
                  type="primary"
                  className="hp-float-right"
                  disabled={props.loading}
                  loading={props.loading}
                  onClick={() => {
                    if (props.currentStep == 0) {
                      form.submit();
                    } else if (props.currentStep == 1) {
                      if (
                        !props.importData.has_errors &&
                        !props.importData.has_validation_errors
                      ) {
                        submitData();
                      } else {
                        Modal.error({
                          icon: (
                            <span className="remix-icon">
                              <RiAlertLine />
                            </span>
                          ),

                          title: (
                            <h5 className="hp-mb-0 hp-font-weight-500">
                              Error
                            </h5>
                          ),
                          content: (
                            <div>
                              <p className="hp-p1-body hp-text-color-black-80">
                                invalid data. please check!
                              </p>
                            </div>
                          ),
                        });
                      }
                    } else {
                      props.nextStep();
                    }
                  }}
                >
                  Next
                </Button>
              )}
              {props.currentStep === steps.length - 1 && (
                <Button
                  type="primary"
                  className="hp-float-right"
                  disabled={props.loading}
                  loading={props.loading}
                  onClick={() => {
                    props.finishStep();
                  }}
                >
                  Done
                </Button>
              )}
              {props.currentStep > 0 && (
                <Button
                  style={{
                    margin: "0 8px",
                  }}
                  disabled={
                    props.currentStep === steps.length - 1 ? true : false
                  }
                  className="hp-float-right"
                  onClick={() => props.prevStep()}
                >
                  Previous
                </Button>
              )}
            </div>
          </Col>
        </Row>
        <Divider className={dividerClass} />
      </Col>
      <Col span={24}>
        <Steps current={props.currentStep}>
          {steps.map((item) => (
            <Step key={item.title} title={item.title} icon={item.icon} />
          ))}
        </Steps>
        <div className="steps-content hp-mt-10">
          {props.currentStep == 0 && (
            <>
              <Card>
                <Form
                  layout="vertical"
                  form={form}
                  onFinish={() => {
                    checkImportData();
                  }}
                >
                  <Row gutter={[12, 12]}>
                    <Col span={24} md={12}>
                      <Title level={4}>Before uploading your file</Title>

                      <Form.Item
                        label="Before uploading your file"
                        name="model_name"
                        rules={[
                          {
                            required: true,
                            message: "Please Select file type",
                          },
                        ]}
                        hasFeedback
                      >
                        <Select
                          showSearch
                          placeholder="Select..."
                          optionFilterProp="model_name"
                          filterOption={(input, option) =>
                            option.children
                              .toLowerCase()
                              .includes(input.toLowerCase())
                          }
                          onChange={(value) => {
                            checkMenuType(value);
                            props.InputChangeValue({
                              key: "model_name",
                              value: value,
                            });
                          }}
                          value={
                            props.exportData.model_name == null
                              ? ""
                              : props.exportData?.model_name
                          }
                        >
                          {props.importMenuItemsList?.menuitems?.map(
                            (menuItem, index) => {
                              return (
                                <Option key={index} value={menuItem.link}>
                                  {menuItem.title}
                                </Option>
                              );
                            }
                          )}
                        </Select>
                      </Form.Item>

                      <List
                        size="small"
                        // bordered
                        dataSource={data}
                        renderItem={(item, key) => (
                          <List.Item>
                            <Text strong>{key + 1}.</Text> {item}
                          </List.Item>
                        )}
                      />
                      {/* <Button
                        className="hp-p-12 hp-mt-10"
                        icon={
                          <RiDownload2Fill className="remix-icon" size={22} />
                        }
                        disabled={
                          props.exportData.model_name == null ? true : false
                        }
                        onClick={() => {
                          // form.submit();
                        }}
                      >
                        Download .csv simple file
                      </Button> */}
                    </Col>
                    <Col span={24} md={12}>
                      <Form.Item
                        // label="Before uploading your file"
                        name="import_file"
                        rules={[
                          {
                            required: true,
                            message: "Please Select import file",
                          },
                        ]}
                        hasFeedback
                      >
                        <Dragger
                          name="file"
                          multiple={false}
                          // action: "https://www.mocky.io/v2/5cc8019d300000980a055e76",

                          onChange={(info) => {
                            console.log("onChange", info);
                            props.InputChangeValue({
                              key: "import_file",
                              value: info,
                            });

                            // const { status } = info.file;

                            // if (status !== "uploading") {
                            //   console.log(info.file, info.fileList);
                            // }

                            // if (status === "done") {
                            //   message.success(
                            //     `${info.file.name} file uploaded successfully.`
                            //   );
                            // } else if (status === "error") {
                            //   message.error(
                            //     `${info.file.name} file upload failed.`
                            //   );
                            // }
                          }}
                          // onDrop={(e) => {
                          //   console.log("Dropped files", e.dataTransfer.files);
                          // }}
                          accept=".csv"
                          showUploadList={false}
                          beforeUpload={(file) => {
                            const reader = new FileReader();
                            reader.onload = (e) => {
                              console.log(e.target.result);
                            };
                            reader.readAsText(file);
                            // Prevent upload
                            return false;
                          }}
                          listType="picture"
                        >
                          <p className="ant-upload-drag-icon">
                            <InboxOutlined size={30} />
                          </p>
                          <p className="ant-upload-text">
                            Click or drag file to this area to upload
                          </p>
                          <p className="ant-upload-hint">
                            Support for a single upload. Strictly prohibit from
                            uploading company data
                          </p>
                        </Dragger>
                      </Form.Item>

                      {props.exportData.import_file != null && (
                        <>
                          <Text>{props.exportData.import_file?.file.name}</Text>
                          <br />
                          <Text strong>
                            File size: {props.exportData.import_file?.file.size}{" "}
                            KB
                          </Text>
                        </>
                      )}
                    </Col>
                  </Row>
                </Form>
              </Card>
            </>
          )}
          {props.currentStep == 1 && (
            <>
              {props.importData.has_errors ? (
                <>
                  <div className="hp-result-col">
                    <Result
                      status="error"
                      title={<h3 className="hp-mb-32">Upload Failed</h3>}
                      icon={<RiCloseCircleFill className="remix-icon" />}
                      extra={null}
                    >
                      {props.importType == 1 ? (
                        <>
                          {props.importData.row_errors.map((error) => {
                            return (
                              <>
                                <div className="desc">
                                  {error.errors.map((e) => {
                                    return (
                                      <>
                                        <Paragraph>
                                          <Text className="h5">{e.error}</Text>
                                        </Paragraph>
                                        <Paragraph>
                                          <RiCloseCircleLine className="remix-icon hp-text-color-danger-1 hp-mr-8" />
                                          {e.row.join(", ")}
                                        </Paragraph>
                                      </>
                                    );
                                  })}
                                </div>
                              </>
                            );
                          })}
                        </>
                      ) : (
                        <>
                          {props.importType == 2 && (
                            <>
                              <Table
                                columns={columns}
                                dataSource={tableData}
                                bordered
                                scroll={{
                                  x: 1300,
                                  y: 500,
                                }}
                                pagination={false}
                                size="middle"
                                // loading={props.loading}
                              />
                            </>
                          )}
                        </>
                      )}
                    </Result>
                  </div>
                </>
              ) : (
                <>
                  <Card>
                    {props.importType == 1 && (
                      <>
                        {props.importData.has_validation_errors ? (
                          <>
                            <Alert
                              className="hp-mb-16"
                              message="Upload Error"
                              description="Data doesn't match the data validation"
                              type="error"
                            />
                          </>
                        ) : (
                          <>
                            <Alert
                              className="hp-mb-16"
                              message="Upload Warning"
                              description="Warning  Alert - Before submitting check your data "
                              type="warning"
                            />
                          </>
                        )}
                      </>
                    )}
                    {props.importType == 2 && (
                      <>
                        <Alert
                          className="hp-mb-16"
                          message="Upload Warning"
                          description="Warning  Alert - Before submitting check your data "
                          type="warning"
                        />
                      </>
                    )}
                    <Table
                      columns={columns}
                      dataSource={tableData}
                      bordered
                      scroll={{
                        x: 1300,
                        y: 500,
                      }}
                      pagination={false}
                      size="middle"
                      // loading={props.loading}
                    />
                  </Card>
                </>
              )}
            </>
          )}
          {props.currentStep == 2 && (
            <Card>
              {props.loading ? (
                <Skeleton />
              ) : (
                <>
                  <Result
                    status={
                      props.submit_ImportData?.has_errors ? "error" : "success"
                    }
                    title={
                      <h3 className="hp-mb-32">
                        {props.submit_ImportData?.success_message}
                      </h3>
                    }
                    icon={
                      props.submit_ImportData?.has_errors ? (
                        <RiCloseCircleFill className="remix-icon" />
                      ) : (
                        <RiCheckboxCircleFill className="remix-icon" />
                      )
                    }
                    extra={null}
                  ></Result>
                </>
              )}
            </Card>
          )}
        </div>
      </Col>
    </Row>
  );
};

function mapStateToProps({ importExport, system }) {
  return {
    importMenuItemsList: system.importMenuItemsList,
    // importModelList: importExport.importModelList,
    exportData: importExport.exportData,
    currentStep: importExport.currentStep,
    importData: importExport.importData,
    importTableColumns: importExport.importTableColumns,
    importTableRowData: importExport.importTableRowData,
    submit_ImportData: importExport.submit_ImportData,
    loading: importExport.loading,
    importType: importExport.importType,
  };
}

export default connect(mapStateToProps, {
  // getImportModelsList,
  getImportMenuItems,
  InputChangeValue,
  uploadImportData,
  uploadImportType2Data,
  nextStep,
  prevStep,
  submitImportData,
  submitImportType2Data,
  finishStep,
  changeType,
  clearData
})(ImportCSV);
