import { React, Fragment, useState, useEffect } from "react";
import { connect } from "react-redux";
import {
  Row,
  Col,
  Divider,
  Card,
  Form,
  Typography,
  Table,
  Pagination,
  Button,
  Popconfirm 
} from "antd";
import { RiErrorWarningLine,  } from "react-icons/ri";
import { DownloadOutlined } from "@ant-design/icons";
import moment from "moment";
import {
  getBackUpDataBaseList,
  setBackUpDataBaseParams,
  getDataBaseBackUP,
  isPasswordModel
} from "../../../../store/settings/BackupDatabse/backupDataBaseSlice";
import BackupPassword from "./backupPassword";

const { Title, Text, Link } = Typography;
const BackUpDataBase = (props) => {
  const columnClass =
    "hp-p1-body hp-text-color-black-100 hp-text-color-dark-0 hp-font-weight-600 ";
  const dividerClass = "hp-border-color-black-40 hp-border-color-dark-80";
  const [form] = Form.useForm();
  useEffect(() => {
    props.getBackUpDataBaseList({ page: 1, search: "", page_size: 10 });
  }, []);
  const columns = [
    {
      title: "S.No",
      width: 100,
      render: (row) => <span className={columnClass}>{row.index}</span>,
    },
    {
      title: "Code",
      width: 150,
      render: (row) => <span className={columnClass}>{row.code}</span>,
    },
    {
      title: "Name",
      width: 250,
      render: (row) => <span className={columnClass}>{row.name}</span>,
    },
    {
      title: "Created On",
      width: 200,
      render: (row) => (
        <span className={columnClass}>
          {moment(row.createdon).format("DD-MM-YYYY, h:mm:ss a")}
        </span>
      ),
    },
    {
      title: "Databse File",
      width: 150,
      render: (row) => (
        <span className={columnClass}>
          {props.userAccessList?.indexOf("System.export_backup") !== -1 && (
          <Button
            type="primary"
            shape="round"
            icon={<DownloadOutlined className="hp-pr-10" />}
            size="small"
            // href={row.file_url}
            onClick={() => {
              // props.isPasswordModel({model:true, db_file: row.file_url});
              props.isPasswordModel({model:true, Id: row.id});

            }}
          >
            Download
          </Button>
          )}
        </span>
      ),
    },
  ];

  const data = props.backUpDataBaselist.map((row, key) => ({
    ...row,
    index:
      (props.backUpDataBaseParams.page - 1) *
        props.backUpDataBaseParams.page_size +
      (key + 1),
  }));

  function showTotal(total) {
    return "Total " + total + " items";
  }
  function onPageChange(page, pageSize) {
    props.getBackUpDataBaseList({
      ...props.backUpDataBaseParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
    props.setBackUpDataBaseParams({
      ...props.backUpDataBaseParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  return (
    <Row>
      <Col span={24}>
        <Row className="hp-mb-16">
          <Col span={16} xs={16} sm={16} md={16}>
            <h4 className="hp-mt-8">Database Backup</h4>
          </Col>
          <Col span={8} xs={8} sm={8} md={8}>
          {props.userAccessList?.indexOf("System.add_backup") !== -1 && (
            <Popconfirm
              placement="topRight"
              title="Are you sure to Create Database  Backup this Record?"
              onConfirm={() => props.getDataBaseBackUP()}
              okText="Yes"
              cancelText="No"
              className="hp-ml-8"
              icon={
                <RiErrorWarningLine className="remix-icon hp-text-color-primary-1" />
              }>
              <Button
                // block
                type="primary"
                className="hp-float-right"
                loading={props.loading}
                disabled={props.loading}
                // onClick={() => props.getDataBaseBackUP()}
                >
                Create Database Backup
              </Button>
          </Popconfirm>
          )}
          </Col>
        </Row>
        <Divider className={dividerClass} />
      </Col>
      <Col span={24}>
        <Card>
          <Table
            className="hp-border-1 hp-border-color-dark-80 hp-border-radius"
            columns={columns}
            dataSource={data}
            pagination={false}
            size="middle"
            loading={props.loading}
          />
          <Pagination
            size="small"
            current={props.backUpDataBaseParams?.page}
            pageSize={props.backUpDataBaseParams?.page_size}
            total={props.listCount}
            showTotal={showTotal}
            showSizeChanger
            // onShowSizeChange={onShowSizeChange}
            onChange={onPageChange}
            on
            className="hp-mb-12 hp-mt-24 hp-float-right"
          />
        </Card>
      </Col>
      <BackupPassword />
    </Row>
  );
};

function mapStateToProps({ backupDatabase, system }) {
  return {
    backUpDataBaselist: backupDatabase.backUpDataBaselist,
    backUpDataBaseParams: backupDatabase.backUpDataBaseParams,
    loading: backupDatabase.loading,
    listCount: backupDatabase.listCount,
    userAccessList: system.userAccessList,
  };
}

export default connect(mapStateToProps, {
  getBackUpDataBaseList,
  setBackUpDataBaseParams,
  getDataBaseBackUP,
  isPasswordModel
})(BackUpDataBase);
