import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import {
  Row,
  Col,
  Divider,
  Form,
  Input,
  Button,
  Drawer,
  Space,
  Select,
  DatePicker,
} from "antd";
import moment, { min } from "moment";

import {
  isDrawerVisible,
   setUsersParams,
  getUsers,
  FilterInputChangeValue
} from "../../../../../store/settings/ManageUsers/manageUserSlice";
import {
  getMiniFirm,
  getMiniDepartment,
} from "../../../../../store/mini/miniSlice";
import {
  getProfileList,
  setProfileParams,
} from "../../../../../store/settings/ManageProfile/profileSlice";

var date = new Date(),
  y = date.getFullYear(),
  m = date.getMonth();

const filter = (props) => {
  const [form] = Form.useForm();
  useEffect(() => {
    props.getProfileList({ page: 1, search: "", page_size: 100 });
    props.getMiniFirm();
    props.getMiniDepartment();
  }, []);
  const userActiveStatus = [
    { id: 1, name: "Active" },
    { id: 0, name: "InActive" },
  ];

  const onFinish = () => {
    props. setUsersParams({
      ...props.usersParams,
      // start_date: props.usersParams.start_date,
      // end_date: props.usersParams.end_date,
      page: 1,
      search: "",
      page_size: 10,
    });

    props.getUsers({
      ...props.usersParams,
      // start_date:
      //   props.usersParams.start_date != null &&
      //   props.usersParams.start_date != ""
      //     ? moment(props.usersParams.start_date).format("YYYY-MM-DD")
      //     : "",
      // end_date:
      //   props.usersParams.end_date != null &&
      //   props.usersParams.end_date != ""
      //     ? moment(props.usersParams.end_date)
      //         .add(1, "days")
      //         .format("YYYY-MM-DD")
      //     : "",
      // start_date: props.usersParams.start_date,
      // end_date: props.usersParams.end_date,
      page: 1,
      search: "",
      page_size: 10,
    });
    props.isDrawerVisible(false);
  };

  function clearFilter() {
    props. setUsersParams({
      start_date: "",
      end_date: "",
      page: 1,
      search: "",
      page_size: 10,
    });
    props.getUsers({
      start_date: "",
      end_date: "",
      page: 1,
      search: "",
      page_size: 10,
    });
    // props.getMiniOccupation();
  }

  function disableDateRanges(range = { startDate: false, endDate: false }) {
    const { startDate, endDate } = range;
    return function disabledDate(current) {
      let startCheck = true;
      let endCheck = true;
      if (startDate) {
        startCheck =
          current &&
          current < moment(startDate + " 00:00:01", "YYYY-MM-DD HH:mm:ss");
      }
      if (endDate) {
        endCheck =
          current &&
          current >
            moment(
              moment(endDate).format("YYYY-MM-DD") + " 23:59:59",
              "YYYY-MM-DD HH:mm:ss"
            );
      }

      return (startDate && startCheck) || (endDate && endCheck);
    };
  }

  useEffect(() => {
    form.setFieldsValue({
      start_date: props.usersParams?.start_date,
      end_date: props.usersParams?.end_date,
      firm: props.usersParams?.firm,
      departments: props.usersParams?.departments,
      user__groups: props.usersParams?.user__groups,
      user__is_active: props.usersParams?.user__is_active,
    });
  }, [props.usersParams]);

  function onSearch(val) {
    props.getUsers({ page: 1, search: val, page_size: 10 });
  }
  return (
    <Row gutter={[24, 24]}>
      <Col span={24}>
        <Drawer
          title="Filters"
          width={420}
          className="hp-drawer-mobile"
          onClose={() => {
            props.isDrawerVisible(false);
          }}
          placement="right"
          visible={props.drawer}
          bodyStyle={{
            paddingBottom: 10,
          }}
        >
          <Form
            form={form}
            layout="vertical"
            name="basic"
            initialValues={{ remember: true }}
            onFinish={onFinish}
          >
            <Row gutter={[12, 12]}>
              <Col span={12}>
                <Form.Item
                  label="From Date"
                  name="start_date"
                  className="hp-mb-10"
                >
                  <DatePicker
                    style={{ width: "100%" }}
                    picker="date"
                    placeholder="Choose Date"
                    format="DD-MM-YYYY"
                    disabledDate={disableDateRanges({
                      endDate: new Date(),
                    })}
                    onChange={(date) => {
                      props.FilterInputChangeValue({
                        key: "start_date",
                        value: date == null ? "" : date,
                      });
                      if (date == null) {
                        props.FilterInputChangeValue({
                          key: "end_date",
                          value: "",
                        });
                      }
                    }}
                  />
                </Form.Item>
              </Col>

              <Col span={12}>
                <Form.Item label="To Date" name="end_date" className="hp-mb-10">
                  <DatePicker
                    style={{ width: "100%" }}
                    picker="date"
                    placeholder="Choose Date"
                    format="DD-MM-YYYY"
                    disabledDate={disableDateRanges({
                      endDate: moment().format("YYYY-MM-DD"),
                      startDate: moment(props.usersParams?.start_date).format(
                        "YYYY-MM-DD"
                      ),
                    })}
                    disabled={
                      props.usersParams.start_date == null ||
                      props.usersParams.start_date == ""
                        ? true
                        : false
                    }
                    onChange={(date) => {
                      props.FilterInputChangeValue({
                        key: "end_date",
                        value: date == null ? "" : date,
                      });
                    }}
                  />
                </Form.Item>
              </Col>

              <Col span={12}>
                <Form.Item
                  label="Firm :"
                  name="firm"
                  // rules={[
                  //   {
                  //     required: true,
                  //     message: "Please Select firm",
                  //   },
                  // ]}
                  // hasFeedback
                >
                  <Select
                    showSearch
                    allowClear
                    placeholder="Select a Firm"
                    optionFilterProp="Firm"
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    defaultActiveFirstOption={true}
                    showArrow={true}
                    onChange={(value) => {
                      props.FilterInputChangeValue({
                        key: "firm",
                        value: value,
                      });
                    }}
                    value={
                      props.usersParams.firm == null
                        ? ""
                        : props.usersParams?.firm
                    }
                  >
                    {props.miniFirmList.map((firm, index) => {
                      return (
                        <Option key={index} value={firm.id}>
                          {firm.name}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>

              <Col span={12}>
                <Form.Item
                  label="Department :"
                  name="departments"
                  // rules={[
                  //   {
                  //     required: true,
                  //     message: "Please Select Department",
                  //   },
                  // ]}
                  // hasFeedback
                >
                  <Select
                    // mode="multiple"
                    allowClear
                    showSearch
                    placeholder="Select a department"
                    optionFilterProp="department"
                    maxTagCount="responsive"
                    defaultActiveFirstOption={true}
                    showArrow={true}
                    onChange={(value) => {
                      props.FilterInputChangeValue({
                        key: "departments",
                        value: value,
                      });
                    }}
                    value={
                      props.usersParams.departments == null
                        ? ""
                        : props.usersParams?.departments
                    }
                    // value={props.usersParams?.departments}
                    options={props.miniDepartmentList.map((dept, index) => {
                      return { label: dept.name, value: dept.id };
                    })}
                  />
                </Form.Item>
              </Col>

              <Col span={12}>
                <Form.Item
                  label="Profiles"
                  name="user__groups"
                  // rules={[
                  //   {
                  //     required: true,
                  //     message: "Please Select Profile",
                  //   },
                  // ]}
                  // hasFeedback
                >
                  <Select
                    // mode="multiple"
                    showSearch
                    allowClear
                    placeholder="Select Profile"
                    optionFilterProp="group_ids"
                    maxTagCount="responsive"
                    onSearch={(value) => {
                      props.setProfileParams({
                        ...props.profileParams,
                        page: 1,
                        search: value,
                      });
                      props.getProfileList({
                        ...props.profileParams,
                        page: 1,
                        search: value,
                      });
                    }}
                    defaultActiveFirstOption={false}
                    showArrow={true}
                    filterOption={false}
                    onChange={(value) => {
                      props.FilterInputChangeValue({
                        key: "user__groups",
                        value: value,
                      });
                      // checkGroupLocation(value);
                    }}
                    // onPopupScroll={() => {
                    //   if (
                    //     props.profileParams.page <
                    //     props.profileParams.no_of_pages
                    //   ) {
                    //     props.setProfileParams({
                    //       ...props.profileParams,
                    //       page: props.profileParams.page + 1,
                    //     });
                    //     props.getProfileList({
                    //       ...props.profileParams,
                    //       page: props.profileParams.page + 1,
                    //     });
                    //   }
                    // }}
                    value={props.usersParams?.user__groups}
                  >
                    {props.profileList.map((profile, index) => {
                      return (
                        <Option key={index} value={profile.id}>
                          {profile.name}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>

              <Col span={12}>
                <Form.Item label="Status" name="user__is_active" className="hp-mb-10">
                  <Select
                    showSearch
                    allowClear
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    placeholder="Select Status"
                    optionFilterProp="user__is_active"
                    onChange={(value) => {
                      props.FilterInputChangeValue({
                        key: "user__is_active",
                        value: value,
                      });
                    }}
                    value={
                      props.usersParams.user__is_active == null
                        ? ""
                        : props.usersParams?.user__is_active
                    }
                  >
                    {userActiveStatus.map((activeInactive, index) => {
                      return (
                        <Option key={index} value={activeInactive.id}>
                          {activeInactive.name}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>
            </Row>
            <Row gutter={[12, 12]}>
              <Col md={12}>
                <Button
                  block
                  type="secondary"
                  className="hp-submit-btn hp-mt-10"
                  onClick={() => {
                    clearFilter();
                  }}
                  htmlType="button"
                >
                  Clear
                </Button>
              </Col>
              <Col md={12}>
                <Button
                  block
                  type="primary"
                  className="hp-submit-btn hp-mt-10"
                  onClick={() => {
                    form.submit();
                  }}
                >
                  Apply
                </Button>
              </Col>
            </Row>
          </Form>
        </Drawer>
      </Col>
    </Row>
  );
};

function mapStateToProps({ manageUsers, mini, manageProfile }) {
  return {
    drawer: manageUsers.drawer,
    usersParams: manageUsers.usersParams,
    miniFirmList: mini.miniFirmList,
    miniDepartmentList: mini.miniDepartmentList,
    profileList: manageProfile.profileList,
    profileParams: manageProfile.profileParams,
  };
}

export default connect(mapStateToProps, {
  isDrawerVisible,
   setUsersParams,
  getUsers,
  FilterInputChangeValue,
  getMiniFirm,
  getMiniDepartment,
  getProfileList,
  setProfileParams,
})(filter);
