import { Fragment, useState, useEffect } from "react";

import { connect } from "react-redux";
import moment from "moment";
import "flatpickr/dist/themes/light.css";
import { RiCloseFill } from "react-icons/ri";
import {
  Button,
  Input,
  Form,
  Modal,
  Badge,
  Row,
  Col,
  Select,
  DatePicker,
} from "antd";
import {
  // countryAdd,
  isPasswordModel,
  InputChangeValue,
  updatePassword,
} from "../../../../store/settings/ManageUsers/manageUserSlice";

export const isObjEmpty = (obj) => Object.keys(obj).length === 0;
const ChangePassword = (props) => {
  const [form] = Form.useForm();

  console.log("props.managerData", props.managerData);

  function ChangeHandeler(e) {
    props.InputChangeValue({
      key: e.target.name,
      value: e.target.value,
    });
  }

  const handleUpdate = () => {
    const obj = {
      id: props.userId,
      password: props.employeeUserdata.password,
    };
    props.updatePassword(obj);
  };

  // Reset Input Values on Close
  const handleResetInputValues = () => {
    props.isPasswordModel(false);
  };

  const FooterAddOrUpdate = () => {
    return (
      <Fragment>
        <Button
          type="primary"
          onClick={() => {
            handleUpdate();
          }}
          block
        >
          Update
        </Button>
      </Fragment>
    );
  };

  const FooterCancel = () => {
    return (
      <Fragment>
        <Button
          onClick={() => {
            handleResetInputValues();
          }}
          type="primary"
          block
          ghost
        >
          Cancel
        </Button>
      </Fragment>
    );
  };

  const modalTitle = <h5 className="modal-title">Password Update</h5>;
  useEffect(() => {
    form.setFieldsValue({
      password: props.employeeUserdata.password,
    });
  }, [props.employeeUserdata]);

  return (
    <Modal
      visible={props.passwordModel}
      title={modalTitle}
      onCancel={() => {
        handleResetInputValues();
        props.isPasswordModel(false);
      }}
      className="hp-modal-p-24"
      footer={
        <Row justify="center">
          <Col span={12} className="hp-pr-4">
            <FooterCancel />
          </Col>

          <Col span={12} className="hp-pl-4">
            <FooterAddOrUpdate />
          </Col>
        </Row>
      }
      centered
      closeIcon={
        <RiCloseFill className="remix-icon text-color-black-100" size={24} />
      }
      width={516}
    >
      <Form
        layout="vertical"
        form={form}
        onFinish={() => {
          if (props.managerData.id == null) {
            handleAdd();
          } else {
            handleUpdate();
          }
        }}
        initialValues={{ remember: true }}
      >
        <Row gutter={[12, 0]}>
          <Col span={24} xs={24} xl={24} md={24}>
            <Form.Item
              label="Password :"
              name="password"
              rules={[
                {
                  required: true,
                  message: "Please Input password",
                },
              ]}
              hasFeedback
            >
              <Input.Password
                id="password"
                name="password"
                style={{ width: "100%" }}
                placeholder="Password"
                value={
                  props.employeeUserdata.name == null
                    ? ""
                    : props.employeeUserdata?.name
                }
                onChange={ChangeHandeler}
                rules={[
                  {
                    required: true,
                    message: "Please input password!",
                  },
                ]}
              />
            </Form.Item>
          </Col>
        </Row>
      </Form>
    </Modal>
  );
};

function mapStateToProps({ manageUsers }) {
  return {
    employeeUserdata: manageUsers.employeeUserdata,
    passwordModel: manageUsers.passwordModel,
    userId: manageUsers.userId,
  };
}

export default connect(mapStateToProps, {
  isPasswordModel,
  InputChangeValue,

  updatePassword,
})(ChangePassword);
