import { Fragment, useState, useEffect } from "react";

import { connect } from "react-redux";
import moment from "moment";
import "flatpickr/dist/themes/light.css";
import { RiCloseFill } from "react-icons/ri";
import {
  Button,
  Input,
  Form,
  Modal,
  Badge,
  Row,
  Col,
  Select,
  DatePicker,
} from "antd";
import {
  // countryAdd,
  isModelVisible,
  InputChangeValue,
  managerAdd,
} from "../../../../store/settings/Managers/ManagersSlice";

export const isObjEmpty = (obj) => Object.keys(obj).length === 0;
const AddManager = (props) => {
  const [form] = Form.useForm();

  console.log("props.managerData", props.managerData);

  function ChangeHandeler(e) {
    props.InputChangeValue({
      key: e.target.name,
      value: e.target.value,
    });
  }

  const handleAdd = () => {
    const obj = {
      id: 0,
      ...props.managerData,
    };
    props.managerAdd(obj);
  };

  const handleUpdate = () => {
    const obj = {
      id: props.managerData.id,
      ...props.managerData,
    };
    props.managerAdd(obj);
  };

  // Reset Input Values on Close
  const handleResetInputValues = () => {
    props.isModelVisible(false);
  };

  const FooterAddOrUpdate = () => {
    if (props.managerData.id == null || props.managerData.id == 0) {
      return (
        <Fragment>
          <Button
            onClick={() => {
              handleAdd();
            }}
            type="primary"
            block
          >
            Add
          </Button>
        </Fragment>
      );
    } else {
      return (
        <Fragment>
          <Button
            type="primary"
            onClick={() => {
              handleUpdate();
            }}
            block
          >
            Update
          </Button>
        </Fragment>
      );
    }
  };

  const FooterCancel = () => {
    return (
      <Fragment>
        <Button
          onClick={() => {
            handleResetInputValues();
          }}
          type="primary"
          block
          ghost
        >
          Cancel
        </Button>
      </Fragment>
    );
  };

  const modalTitle = (
    <h5 className="modal-title">
      {props.managerData.id == null || props.managerData.id == 0
        ? "Add"
        : "Update"}{" "}
      Manager
    </h5>
  );
  useEffect(() => {
    form.setFieldsValue({
      name: props.managerData.name,
    });
  }, [props.managerData]);

  return (
    <Modal
      visible={props.model}
      title={modalTitle}
      onCancel={() => {
        handleResetInputValues();
        props.isModelVisible(false);
      }}
      className="hp-modal-p-24"
      footer={
        <Row justify="center">
          <Col span={12} className="hp-pr-4">
            <FooterCancel />
          </Col>

          <Col span={12} className="hp-pl-4">
            <FooterAddOrUpdate />
          </Col>
        </Row>
      }
      centered
      closeIcon={
        <RiCloseFill className="remix-icon text-color-black-100" size={24} />
      }
      width={516}
    >
      <Form
        layout="vertical"
        form={form}
        onFinish={() => {
          if (props.managerData.id == null) {
            handleAdd();
          } else {
            handleUpdate();
          }
        }}
        initialValues={{ remember: true }}
      >
        <Row gutter={[12, 0]}>
          <Col span={24} xs={24} xl={24} md={24}>
            <Form.Item
              label="Manager Name :"
              name="name"
              rules={[
                {
                  required: true,
                  message: "Please Input manager name",
                },
                {
                  pattern: new RegExp(/[a-z][A-Z]$/i),
                  message: "field does not accept numbers",
                },
              ]}
              hasFeedback
            >
              <Input
                id="name"
                name="name"
                style={{ width: "100%" }}
                placeholder="Manager name"
                value={
                  props.managerData.name == null ? "" : props.managerData?.name
                }
                maxLength={20}
                onChange={ChangeHandeler}
                rules={[
                  {
                    required: true,
                    message: "Please input your Manager name!",
                  },
                ]}
              />
            </Form.Item>
          </Col>
        </Row>
      </Form>
    </Modal>
  );
};

function mapStateToProps({ managers }) {
  return {
    managerData: managers.managerData,
    model: managers.model,
  };
}

export default connect(mapStateToProps, {
  isModelVisible,
  InputChangeValue,

  managerAdd,
})(AddManager);
