import React from "react";
import { connect } from "react-redux";
import { Row, Col, Divider, Form, Input, Button,Upload, message } from "antd";
import {
  getManageCompany,
  isModelVisible,
  InputChangeValue,
  getCompanyLogoUpload,
} from "../../../../store/settings/ManageCompany/manageCompanySlice";
import { useEffect } from "react";
import AddManageCompany from "./addManageCompany";
import {
  EditOutlined,
  LoadingOutlined,
  PlusOutlined
} from "@ant-design/icons";

function ManageCompany(props) {
  const dividerClass = "hp-border-color-black-40 hp-border-color-dark-80";
  const listTitle = "hp-p1-body";
  const listResult =
    "hp-mt-sm-4 hp-p1-body hp-text-color-black-100 hp-text-color-dark-0";

    function CheckImageSize(file) {
      var confirmPromiseResolve, confirmPromiseReject;
      var img = new Image();
      img.src = window.URL.createObjectURL(file);
      img.onload = function() {
          var width = img.naturalWidth, height = img.naturalHeight;
          window.URL.revokeObjectURL( img.src );
          if( width == 300 && height == 150 ) {
              if(confirmPromiseResolve){
                  confirmPromiseResolve(true);
              }
          } else { 
              if(confirmPromiseResolve){
                  confirmPromiseResolve(false);
              }
          } 
      }; 
  
      return new Promise(function(resolve, reject){
  
         confirmPromiseResolve = resolve;
         confirmPromiseReject = reject;
      });
   }
  
  
  

    const beforeUpload = async (file) => {
      const isJpgOrPng = file.type === "image/jpeg" || file.type === "image/png";
      if (!isJpgOrPng) {
        message.error("You can only upload JPG/PNG file!");
      }
  
      const isLt2M = file.size / 1024 / 1024 < 2;
       if (!isLt2M) {
        message.error("Image must smaller than 2MB!");
      }

     
      var isImageOk = await CheckImageSize( file )
      console.log("imageee", isImageOk);
      if (!isImageOk) {
        message.error("Image dimensions should be 300 X 150!");
      }
  
      return isJpgOrPng && isLt2M && isImageOk;
    };
  
    const uploadButton = (
      <>
        {" "}
        <div className="hp-center hp-mt-0 ">
          {props.companyLogoLoading ? (
            <LoadingOutlined />
          ) : (
            <PlusOutlined className="hp-mt-16" />
          )}
          <div className="hp-mt-8 hp-mb-16">Upload</div>
        </div>
      </>
    );

    useEffect(() => {
      props.getManageCompany();
    }, [])

  return (
    <Row>
      <Col span={24}>
        <Row className="hp-mb-16">
          <Col span={16} xs={16} sm={16} md={16}>
            <h4 className="hp-mt-8">Manage Company</h4>
          </Col>
          <Col span={8} xs={8} sm={8} md={8}>
            <Button
              type="primary"
              className="hp-float-right"
              onClick={() => props.isModelVisible(true)}
            >
              Edit Manage Company
            </Button>
          </Col>
        </Row>
        <Divider className={dividerClass} />
      </Col>

      <Col span={24} md={24} className="hp-profile-content-list  hp-pb-sm-0 ">
        
        <Row>
          <Col md={6} span={24} className="hp-mr-16">
            <h5 className="hp-mt-8"> Company Logo</h5>
            {/* <img
              alt="example"
              style={{ width: "100%" }}
              src={props.manageCompanyData?.COMPANY__LOGO}
            /> */}
            <Upload
              listType="picture-card hp-w-100"
              className="avatar-uploader "
              showUploadList={false}
              accept="image/*"
              beforeUpload={beforeUpload}
              customRequest={(info) => {
                props.getCompanyLogoUpload({
                  file: info.file,
                  imageType: "COMPANY__LOGO",
                });
              }}
            >
              {props.manageCompanyLogo.COMPANY__LOGO != null ? (
                <div className="hp-d-flex">
                  <img
                    className="ant-upload-select-picture-card hp-preview-img"
                    src={props.manageCompanyLogo.COMPANY__LOGO}
                    alt="avatar"
                    style={{
                      width: "100%",
                    }}
                  />
                  <EditOutlined/>
                </div>
              ) : (
                uploadButton
              )}
            </Upload>
          </Col>

          <Col md={4} span={24}>
            <h5 className="hp-mt-8"> Company Small Logo</h5>
            {/* <img
              alt="example"
              style={{ width: "100%" }}
              src={props.manageCompanyData?.COMPANY__SMALLLOGO}
            /> */}

            <Upload
              listType="picture-card hp-w-100"
              className="avatar-uploader"
              showUploadList={false}
              accept="image/*"
              beforeUpload={beforeUpload}
              customRequest={(info) => {
                props.getCompanyLogoUpload({
                  file: info.file,
                  imageType: "COMPANY__SMALLLOGO",
                });
              }}
            >
              {props.manageCompanyLogo.COMPANY__SMALLLOGO != null ? (
                <div className="hp-d-flex">
                  <img
                    className="ant-upload-select-picture-card hp-preview-img"
                    src={props.manageCompanyLogo.COMPANY__SMALLLOGO}
                    alt="avatar"
                    style={{
                      width: "100%",
                    }}
                  />
                  <EditOutlined/>
                </div>
              ) : (
                uploadButton
              )}
            </Upload>
          </Col>
        </Row>
        <Divider />
        <Col
          span={24}
          className="hp-profile-content-list hp-ml-12 hp-mt-8 hp-pb-sm-0 hp-pb-120"
        >
          <ul>
            <li>
              <span className={listTitle}>Company Name</span>
              <span className={listResult}>
                {props.manageCompanyData?.COMPANY__NAME == "" ||
                props.manageCompanyData?.COMPANY__NAME == null
                  ? "-------"
                  : props.manageCompanyData?.COMPANY__NAME}
              </span>
            </li>

            <li className="hp-mt-18">
              <span className={listTitle}>Company Sample Name</span>
              <span className={listResult}>
                {props.manageCompanyData?.COMPANY__SIMPLENAME == "" ||
                props.manageCompanyData?.COMPANY__SIMPLENAME == null
                  ? "-------"
                  : props.manageCompanyData?.COMPANY__SIMPLENAME}
              </span>
            </li>

            <li className="hp-mt-18">
              <span className={listTitle}>Company Email</span>
              <a className={listResult} href="mailto:******@gmail.com">
                {props.manageCompanyData?.COMPANY__EMAIL == "" ||
                props.manageCompanyData?.COMPANY__EMAIL == null
                  ? "-------"
                  : props.manageCompanyData?.COMPANY__EMAIL}
              </a>
            </li>

            <li className="hp-mt-18">
              <span className={listTitle}>Company Mobile Number</span>
              <a className={listResult} href="tel:+9999999999">
                {props.manageCompanyData?.COMPANY__MOBILE == "" ||
                props.manageCompanyData?.COMPANY__MOBILE == null
                  ? "-------"
                  : props.manageCompanyData?.COMPANY__MOBILE}
              </a>
            </li>

            <li className="hp-mt-18">
              <span className={listTitle}>Company Alternate Mobile Number</span>
              <a className={listResult} href="tel:+9999999999">
                {props.manageCompanyData?.COMPANY__ALTERNATEMOBILE == "" ||
                props.manageCompanyData?.COMPANY__ALTERNATEMOBILE == null
                  ? "-------"
                  : props.manageCompanyData?.COMPANY__ALTERNATEMOBILE}
              </a>
            </li>

            <li className="hp-mt-18">
              <span className={listTitle}>Company GST Number</span>
              <span className={listResult}>
                {props.manageCompanyData?.COMPANY__GSTNO == "" ||
                props.manageCompanyData?.COMPANY__GSTNO == null
                  ? "-------"
                  : props.manageCompanyData?.COMPANY__GSTNO}
              </span>
            </li>

            <li className="hp-mt-18">
              <span className={listTitle}>Company Website</span>
              <span className={listResult}>
                {props.manageCompanyData?.COMPANY__WEBSITE == "" ||
                props.manageCompanyData?.COMPANY__WEBSITE == null
                  ? "-------"
                  : props.manageCompanyData?.COMPANY__WEBSITE}
              </span>
            </li>

            <li className="hp-mt-18">
              <span className={listTitle}>Company Address</span>
              <span className={listResult}>
                {props.manageCompanyData?.COMPANY__ADDRESS == "" ||
                props.manageCompanyData?.COMPANY__ADDRESS == null
                  ? "-------"
                  : props.manageCompanyData?.COMPANY__ADDRESS}
              </span>
            </li>
          </ul>
        </Col>
      </Col>
      <AddManageCompany />
    </Row>
  );
}

function mapStateToProps({manageCompany}) {
  return {
    manageCompanyData: manageCompany.manageCompanyData,
    manageCompanyLogo: manageCompany.manageCompanyLogo,
    companyLogoLoading: manageCompany.companyLogoLoading
  };
}

export default connect(mapStateToProps, {
  getManageCompany,
  isModelVisible,
  getCompanyLogoUpload
})(ManageCompany);
