import React, {useEffect } from "react";
import { connect } from "react-redux";
import { Row, Col, Divider, Form, Input, Button, Select, Switch  } from "antd";
import {
  appSettingsAdd,
  InputChangeValue,
  getAppSettings
} from "../../../store/settings/AppSettings/appsettingsSlice"


 function AppSettings(props) {
  const dividerClass = "hp-border-color-black-40 hp-border-color-dark-80";
  const [form] = Form.useForm();

  function ChangeHandeler(e) {
    props.InputChangeValue({
      key: e.target.name,
      value: e.target.value,
    });
  }

  useEffect(() => {
    props.getAppSettings();
  }, [])
  

  const handleUpdate = () => {
    const obj = {
      ...props.appSettingsData,
    };
    props.appSettingsAdd(obj);
  };

  const backendList = [
    { id: 1, name: "django.core.mail.backends.smtp.EmailBackend" },
  ];

   // Reset Input Values on Close
  //  const handleResetInputValues = () => {
  //   props.isModelVisible(false);
  // };


  // const onFinish = () => {
  //   const obj = {
  //     ...props.appSettingsData,
  //   };
  //   props.appSettingsAdd(obj);
  // };

  // const onFinishFailed = (errorInfo) => {
  //   console.log("Failed:", errorInfo);
  // };


  useEffect(() => {
    form.setFieldsValue({
      SMS__MSG_VAR: props.appSettingsData.SMS__MSG_VAR,
      SMS__NUMBER_VAR: props.appSettingsData.SMS__NUMBER_VAR,
      SMS__URL: props.appSettingsData.SMS__URL,
      SMTP__BACKEND: props.appSettingsData.SMTP__BACKEND,
      SMTP__HOST: props.appSettingsData.SMTP__HOST,
      SMTP__PASSWORD: props.appSettingsData.SMTP__PASSWORD,
      SMTP__PORT: props.appSettingsData.SMTP__PORT,
      SMTP__USER: props.appSettingsData.SMTP__USER,
      SMTP__USE_TLS: props.appSettingsData.SMTP__USE_TLS,
      THIRDPARTY__URL: props.appSettingsData.THIRDPARTY__URL,
      THIRDPARTY__TOKEN: props.appSettingsData.THIRDPARTY__TOKEN,
    });
  }, [props.appSettingsData]);

  return (
    <Row>
      <Col span={24}>
        <h4 className="hp-mt-8">SMS</h4>
        <Divider className={dividerClass} />
      </Col>

      <Col span={24}>
        <Form
          layout="vertical"
          form={form}
          onFinish={() => {
            handleUpdate();
          }}
          initialValues={{ remember: true }}
        >
          <Row gutter={[12, 12]}>
            <Col span={24} xs={24} xl={8} md={8}>
              <Form.Item
                label="Message Variable:"
                name="SMS__MSG_VAR"
                rules={[
                  {
                    required: true,
                    message: "Please Input SMS Message Variable",
                  },
                ]}
                hasFeedback
              >
                <Input
                  id="SMS__MSG_VAR"
                  name="SMS__MSG_VAR"
                  style={{ width: "100%" }}
                  placeholder="Message Variable"
                  value={
                    props.appSettingsData.SMS__MSG_VAR == null
                      ? ""
                      : props.appSettingsData?.SMS__MSG_VAR
                  }
                  // value={props.appSettingsData?.SMS__MSG_VAR}
                  onChange={ChangeHandeler}
                />
              </Form.Item>
            </Col>
            <Col span={24} xs={24} xl={8} md={8}>
              <Form.Item
                label="Number Variable :"
                name="SMS__NUMBER_VAR"
                rules={[
                  {
                    required: true,
                    message: "Please Input Number Variable",
                  },
                ]}
                hasFeedback
              >
                <Input
                  id="SMS__NUMBER_VAR"
                  name="SMS__NUMBER_VAR"
                  style={{ width: "100%" }}
                  placeholder="Number Variable"
                  value={
                    props.appSettingsData.SMS__NUMBER_VAR == null
                      ? ""
                      : props.appSettingsData?.SMS__NUMBER_VAR
                  }
                  onChange={ChangeHandeler}
                />
              </Form.Item>
            </Col>
            <Col span={24} xs={24} xl={8} md={8}>
              <Form.Item
                label="URL:"
                name="SMS__URL"
                rules={[
                  {
                    required: true,
                    message: "Please Input URL",
                  },
                ]}
                hasFeedback
              >
                <Input
                  id="SMS__URL"
                  name="SMS__URL"
                  style={{ width: "100%" }}
                  placeholder=" URL"
                  value={
                    props.appSettingsData.SMS__URL == null
                      ? ""
                      : props.appSettingsData?.SMS__URL
                  }
                  onChange={ChangeHandeler}
                />
              </Form.Item>
            </Col>

            <Col span={24}>
              <p className="hp-text-color-primary-1 hp-font-weight-500">
                {props.appSettingsData.SMS__URL +
                  "&" +
                  props.appSettingsData.SMS__MSG_VAR +
                  "=XXXXXXXXXXXXXXXXXXX&" +
                  props.appSettingsData.SMS__NUMBER_VAR +
                  "=XXXXXXXXX"}
              </p>
            </Col>

            <Col span={24}>
              <h4 className="hp-mt-8">EMAIL CONFIGURATION</h4>
              <Divider className={dividerClass} />
            </Col>

            <Col span={24} xs={24} xl={8} md={8}>
              <Form.Item
                label="Backend"
                name="SMTP__BACKEND"
                rules={[
                  {
                    required: true,
                    message: "Please Select Backend",
                  },
                ]}
                hasFeedback
              >
                <Select
                  showSearch
                  placeholder="Select Backend"
                  optionFilterProp="SMTP__BACKEND"
                  // disabled
                  filterOption={(input, option) =>
                    option.children.toLowerCase().includes(input.toLowerCase())
                  }
                  onChange={(value) => {
                    props.InputChangeValue({
                      key: "SMTP__BACKEND",
                      value: value,
                    });
                  }}
                  value={
                    props.appSettingsData.SMTP__BACKEND == null
                      ? ""
                      : props.appSettingsData?.SMTP__BACKEND
                  }
                >
                  {backendList.map((SMTP__BACKEND, index) => {
                    return (
                      <Option key={index} value={SMTP__BACKEND.name}>
                        {SMTP__BACKEND.name}
                      </Option>
                    );
                  })}
                </Select>
              </Form.Item>
            </Col>

            <Col span={24} xs={24} xl={8} md={8}>
              <Form.Item
                label="Host:"
                name="SMTP__HOST"
                rules={[
                  {
                    required: true,
                    message: "Please Input HOST",
                  },
                ]}
                hasFeedback
              >
                <Input
                  id="SMS__URL"
                  name="SMTP__HOST"
                  style={{ width: "100%" }}
                  placeholder="HOST"
                  value={
                    props.appSettingsData.SMTP__HOST == null
                      ? ""
                      : props.appSettingsData?.SMTP__HOST
                  }
                  onChange={ChangeHandeler}
                />
              </Form.Item>
            </Col>

            <Col span={24} xs={24} xl={8} md={8}>
              <Form.Item
                label="Port:"
                name="SMTP__PORT"
                rules={[
                  {
                    required: true,
                    message: "Please Input Port",
                  },
                ]}
                hasFeedback
              >
                <Input
                  id="SMS__URL"
                  name="SMTP__PORT"
                  style={{ width: "100%" }}
                  placeholder="Port"
                  value={
                    props.appSettingsData.SMTP__PORT == null
                      ? ""
                      : props.appSettingsData?.SMTP__PORT
                  }
                  onChange={ChangeHandeler}
                />
              </Form.Item>
            </Col>

            <Col span={24} xs={24} xl={8} md={8}>
              <Form.Item
                label="Use TLS:"
                name="SMTP__USE_TLS"
                rules={[
                  {
                    required: true,
                    message: "Please Input Use TLS",
                  },
                ]}
                hasFeedback
              >
                {/* <Input
                  id="SMTP__USE_TLS"
                  name="SMTP__USE_TLS"
                  style={{ width: "100%" }}
                  placeholder="Use TLS"
                  value={
                    props.appSettingsData.SMTP__USE_TLS == null
                      ? ""
                      : props.appSettingsData?.SMTP__USE_TLS
                  }
                  onChange={ChangeHandeler}
                /> */}
                <Switch
                  checkedChildren="On"
                  unCheckedChildren="Off"
                  defaultChecked SMTP__USE_TLS
                  className="hp-mb-16"
                  checked={props.appSettingsData.SMTP__USE_TLS}
                  onChange={(value) => {
                    props.InputChangeValue({
                      key: "SMTP__USE_TLS",
                      value: value,
                    });
                  }}
                />
              </Form.Item>
            </Col>

            <Col span={24} xs={24} xl={8} md={8}>
              <Form.Item
                label="User:"
                name="SMTP__USER"
                rules={[
                  {
                    required: true,
                    message: "Please Input User",
                  },
                ]}
                hasFeedback
              >
                <Input
                  id="SMS__URL"
                  name="SMTP__USER"
                  style={{ width: "100%" }}
                  placeholder="User"
                  value={
                    props.appSettingsData.SMTP__USER == null
                      ? ""
                      : props.appSettingsData?.SMTP__USER
                  }
                  onChange={ChangeHandeler}
                />
              </Form.Item>
            </Col>

            <Col span={24} xs={24} xl={8} md={8}>
              <Form.Item
                label="Password:"
                name="SMTP__PASSWORD"
                rules={[
                  {
                    required: true,
                    message: "Please Input Password",
                  },
                ]}
                hasFeedback
              >
                <Input.Password
                  id="SMS__URL"
                  name="SMTP__PASSWORD"
                  style={{ width: "100%" }}
                  placeholder="Password"
                  value={
                    props.appSettingsData.SMTP__PASSWORD == null
                      ? ""
                      : props.appSettingsData?.SMTP__PASSWORD
                  }
                  onChange={ChangeHandeler}
                />
              </Form.Item>
            </Col>

            <Col span={24}>
              <h4 className="hp-mt-8">3rd Party</h4>
              <Divider className={dividerClass} />
            </Col>

            <Col span={24} xs={24} xl={8} md={8}>
              <Form.Item
                label="Third Party URL:"
                name="THIRDPARTY__URL"
                rules={[
                  {
                    required: true,
                    message: "Please Input Third Party URL",
                  },
                ]}
                hasFeedback
              >
                <Input
                  id="THIRDPARTY__URL"
                  name="THIRDPARTY__URL"
                  style={{ width: "100%" }}
                  placeholder="Third Party URL"
                  value={
                    props.appSettingsData.THIRDPARTY__URL == null
                      ? ""
                      : props.appSettingsData?.THIRDPARTY__URL
                  }
                  onChange={ChangeHandeler}
                />
              </Form.Item>
            </Col>

            <Col span={24} xs={24} xl={8} md={8}>
              <Form.Item
                label="Third Party Token:"
                name="THIRDPARTY__TOKEN"
                rules={[
                  {
                    required: true,
                    message: "Please Input Third Party Token",
                  },
                ]}
                hasFeedback
              >
                <Input
                  id="THIRDPARTY__TOKEN"
                  name="THIRDPARTY__TOKEN"
                  style={{ width: "100%" }}
                  placeholder="Third Party Token"
                  value={
                    props.appSettingsData.THIRDPARTY__TOKEN == null
                      ? ""
                      : props.appSettingsData?.THIRDPARTY__TOKEN
                  }
                  onChange={ChangeHandeler}
                />
              </Form.Item>
            </Col>
          </Row>
          <Button
            type="primary"
            className="hp-submit-btn hp-mt-10"
            htmlType="submit"
            // disabled={props.formSubmitLoading}
          >
            Submit
          </Button>
        </Form>
      </Col>
    </Row>
  );
}
function mapStateToProps({appSettings}) {
  return {
    appSettingsData: appSettings.appSettingsData
  };
}

export default connect(mapStateToProps, {
  appSettingsAdd,
  InputChangeValue,
  getAppSettings
})(AppSettings);