import { React, Fragment, useState, useEffect } from "react";

import { connect } from "react-redux";

import {
  Row,
  Col,
  Divider,
  Steps,
  Card,
  Upload,
  Form,
  Input,
  Button,
  message,
  Typography,
  Select,
  List,
  Table,
  Result,
  Skeleton,
} from "antd";

import {
  RiDownload2Fill,
  RiCheckboxCircleFill,
  RiCloseCircleFill,
  RiCloseCircleLine,
} from "react-icons/ri";
import {
  // getExportModelsList,
  InputChangeValue,
  downloadExportData,
  clearData
} from "../../../store/settings/ImportExport/importExportSlice";

import {getExportMenuItems} from "../../../store/system/systemSlice";

const { Title, Text, Link } = Typography;
const ExportCSV = (props) => {
  const dividerClass = "hp-border-color-black-40 hp-border-color-dark-80";
  const [form] = Form.useForm();
  useEffect(() => {
    props.clearData();
    props.getExportMenuItems();
  }, []);
  console.log("exportModelsList", props.exportData);

  const data = [
    "Reaching the targets and goals set for my area.",
    "Servicing the needs of my existing customers.",
    "Maintaining the relationships with existing customers for repeat business.",
    "Reporting to top managers",
    "Keeping up to date with the products.",
  ];
  function exportData() {
    var objData = {
      model_name: props.exportData.model_name,
      file_format: 0,
    };
    props.downloadExportData(objData);
  }



  useEffect(() => {
    form.setFieldsValue({
      model_name: props.exportData.model_name,
    });
  }, [props.exportData]);
  return (
    <Row>
      <Col span={24}>
        <Row className="hp-mb-16">
          <Col span={16} xs={16} sm={16} md={16}>
            <h4 className="hp-mt-8">Export CSV</h4>
          </Col>
        </Row>
        <Divider className={dividerClass} />
      </Col>
      <Col span={24}>
        <Card>
          <Form
            layout="vertical"
            form={form}
            onFinish={() => {
              exportData();
            }}
          >
            <Row gutter={[12, 12]}>
              <Col span={24} md={12}>
                <Title level={4}>Before Export your file</Title>
                <List
                  size="small"
                  // bordered
                  dataSource={data}
                  renderItem={(item, key) => (
                    <List.Item>
                      <Text strong>{key + 1}.</Text> {item}
                    </List.Item>
                  )}
                />
              </Col>
              <Col span={24} md={12}>
                <Form.Item
                  label="Before Export your file"
                  name="model_name"
                  rules={[
                    {
                      required: true,
                      message: "Please Select file type",
                    },
                  ]}
                  hasFeedback
                >
                  <Select
                    showSearch
                    placeholder="Select..."
                    optionFilterProp="model_name"
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    onChange={(value) => {
                      props.InputChangeValue({
                        key: "model_name",
                        value: value,
                      });
                    }}
                    value={
                      props.exportData.model_name == null
                        ? ""
                        : props.exportData?.model_name
                    }
                  >
                    {props.exportMenuItemsList?.menuitems?.map((menuItem, index) => {
                      return (
                        <Option key={index} value={menuItem.link}>
                          {menuItem.title}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>

                <Button
                  className="hp-p-12 hp-mt-10 hp-float-right"
                  icon={<RiDownload2Fill className="remix-icon" size={22} />}
                  disabled={props.loading}
                  loading={props.loading}
                  onClick={() => {
                    form.submit();
                  }}
                >
                  Export .csv file
                </Button>
              </Col>
            </Row>
          </Form>
        </Card>
      </Col>
    </Row>
  );
};

function mapStateToProps({ importExport, system }) {
  return {
    // exportModelList: importExport.exportModelList,
    exportData: importExport.exportData,
    loading: importExport.loading,
    exportMenuItemsList: system.exportMenuItemsList
  };
}

export default connect(mapStateToProps, {
  clearData,
  InputChangeValue,
  downloadExportData,
  getExportMenuItems
})(ExportCSV);
